.taxi {
	@extend %window;

	background-image: url(../../../images/job-bg.jpg);
	width: 50%;
	padding: 1% 2% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_title {
		width: 65%;
	}

	.remark {
		margin: 0;
		margin-bottom: 4%;
		margin-top: 5%;
		color: $gold;
		text-align: center;
		font-size: 1.2vw;
		font-weight: 300;
	}

	&_footer {
		display: flex;
		justify-content: space-between;
		width: 100%;
	}
}
