.vehicle-sell {
	@extend %window;

	display: flex;
	flex-direction: column;
	align-items: center;
	background-image: url(../../../images/vehsell-bg.jpg);
	width: 50%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_container {
		margin-bottom: 5%;
		padding-top: 5%;
		padding-left: 4%;
		width: 100%;
		text-align: left;
		border-bottom: 1px solid $gold;
	}

	h3 {
		margin: 0;
		width: 100%;
		font-size: 1.5vw;
		font-weight: 300;
	}

	&_price {
		font-size: 3.2vw;
		font-weight: 700;
		color: $gold;
	}

	&_footer {
		display: flex;
		justify-content: center;
		width: 80%;

		button {
			width: 30%;

			&:first-of-type {
				margin-right: 1%;
			}
		}
	}
}
