@import './info';

.car-shop {
	margin-left: auto;
	padding: 0.8% 1%;
	width: 25%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-image: url(../../../../images/carshop-bg.jpg);
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	&--motoshop {
		background-image: url(../../../../images/motoshop-bg.jpg);
	}

	&--boatshop {
		background-image: url(../../../../images/boatshop_bg.jpg);
	}

	&--airshop {
		background-image: url(../../../../images/airshop_bg.jpg);
	}

	&_title {
		margin: 0 auto;
		width: 80%;
	}

	&_search {
		position: relative;
		margin: 0 auto;
		margin-top: 5%;
		width: 80%;
		text-align: center;

		input {
			background: transparent;
			border: 1px solid #fff;
			width: 100%;
			padding: 2% 10px;
			padding-left: 10%;
			margin-bottom: 5px;
			outline: none;
			color: #fff;
			font-family: inherit;
			font-size: 1vw;

			+ svg {
				position: absolute;
				left: 2%;
				top: 10%;
				width: 1.2vw;
				height: 1.2vw;
				color: $gold;
			}
		}
	}

	&_filter {
		position: relative;
		margin: 0 auto;
		padding-right: 4%;
		width: 80%;
		display: flex;

		svg {
			position: absolute;
			top: 0;
			right: 0;
			color: $gold;
		}

		button {
			@extend %button;

			margin-left: auto;
			color: #fff;
			font-size: 1vw;
			border-bottom: 1px solid #fff;
			text-align: left;
			padding: 0;
			margin-right: 5px;
		}

		&-list {
			@extend %list;

			position: absolute;
			top: 100%;
			right: 0;
			padding: 2% 8%;
			background: #fff;
			outline: 1px solid $purple;
			z-index: 10;

			li {
				position: relative;
				margin-bottom: 4%;
				padding-left: 10px;
				text-align: left;
				color: $purple;
				font-size: 1vw;

				&:hover {
					opacity: 0.8;
				}

				&::before {
					@extend %pseudo;

					top: 20%;
					left: -8%;
					width: 0.8vw;
					height: 0.8vw;
					border: 1px solid $gold;
				}

				&.active {
					&:hover {
						opacity: 1;
					}
				}

				&.active::before {
					background: $gold;
				}
			}
		}
	}

	&_list {
		margin-bottom: 10%;
		padding-top: 4%;
		padding-right: 1%;
		height: 58vh;
		overflow-y: scroll;

		&-item {
			border: 1px solid #fff;
			padding: 1% 2%;
			margin-bottom: 5px;
			font-weight: 400;
			text-align: left;
			font-size: 1vw;
			transition: all 0.5s ease;

			&:hover {
				opacity: 0.7;
			}

			&.is-active {
				background: #fff;
				font-weight: 700;
				color: $purple;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	&_footer {
		width: 100%;

		.buttons {
			padding-top: 4%;
			display: flex;
			justify-content: space-between;

			button {
				width: 49%;
			}
		}
	}

	&_price {
		width: 100%;
		border-top: 1px solid $gold;
	}

	&_test-btn {
		position: absolute;
		width: 15% !important;
		right: 26%;
	}

	.cam_controls {
		position: absolute;
		bottom: 1%;
		left: 1%;
		padding: 2%;
		width: 20%;

		h4 {
			font-size: 1.2vw;
			font-weight: 200;
			margin: 0;
			margin-top: 2%;
			text-align: left;
			padding-left: 2%;
		}
	}
}
