.business {
	@extend %window;

	width: 55%;
	padding: 1% 4%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: url(../../../images/business-bg.jpg);

	&--owned {
		width: 60%;

		.business {
			&_info {
				margin-left: auto;
				width: 70%;
			}
		}
	}

	&_price {
		padding-top: 2%;
		margin-bottom: 4%;
		display: block;
		text-align: center;
		font-size: 3vw;
		font-weight: 700;
		color: $gold;
	}

	&_info {
		margin-bottom: 4%;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 1.2vw;
		font-weight: 300;

		&-descr {
			padding-left: 5%;

			p {
				margin: 0;
				margin-bottom: 5px;
				text-align: left;
			}

			span {
				color: $gold;
			}
		}
	}

	&_container {
		padding-top: 2%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
	}

	&_tab-list {
		margin-bottom: 5%;
		display: flex;
		flex-direction: column;
	}

	&_tab-item {
		margin-bottom: 4%;
		font-size: 1.5vw;

		&.active {
			background: $gold;
			border-color: $gold;
			color: $purple;

			&:hover {
				opacity: 1;
			}
		}
	}

	&_tab {
		width: 70%;
	}

	&_submit-btn {
		display: block;
		margin: 0 auto;
		margin-top: 5%;
	}

	&_progress {
		position: relative;
		width: 30%;
		height: 11vw;

		&-circle {
			.time {
				position: absolute;
				top: 45%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 1.3vw;
				font-weight: 700;
				color: $gold;
			}

			.status {
				position: absolute;
				top: 60%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 1vw;
				color: $gold;
			}
		}

		&-btn {
			height: inherit;
			border: 4px solid $gold;
			border-radius: 50%;
			background: $gold;
			text-align: center;
			color: $purple;
			display: flex;
			flex-direction: column;
			justify-content: center;

			.time {
				font-size: 1.3vw;
				font-weight: 700;
				margin-bottom: 1%;
			}

			.status {
				font-size: 1vw;
			}
		}
	}

	&_tax {
		display: flex;
		flex-direction: column;
		align-items: center;

		input {
			margin-bottom: 2%;
			width: 100%;
			background: none;
			border: none;
			border-bottom: 2px solid $gold;
			color: #fff;
			font-family: inherit;
			font-weight: 300;
			font-size: 2.2vw;
			text-align: center;
			outline: none;

			&::placeholder {
				color: #fff;
			}
		}

		&-price {
			width: 100%;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			margin-left: auto;
			text-align: right;

			h4 {
				font-weight: 300;
				font-size: 1.6vw;
				margin: 0;
			}

			.price {
				padding-left: 10px;
				font-size: 1.4vw;
				font-weight: 700;
				color: $gold;
			}
		}
	}

	&_gov-sell {
		padding-bottom: 4%;
		display: flex;
		justify-content: center;
		align-items: center;

		h2 {
			margin: 0;
			margin-right: 15px;
			font-weight: 300;
			font-size: 1.8vw;
		}

		span {
			width: 50%;
			font-size: 2.4vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
			border-bottom: 1px solid $gold;
		}

		input {
			border: none;
			border-bottom: 1px solid $gold;
			background: transparent;
			outline: none;
			width: 40%;
			font-size: 2vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
		}
	}

	&_sell {
		padding: 0 8%;

		label {
			display: flex;
			justify-content: space-between;
		}

		h2 {
			margin: 0;
			margin-right: 15px;
			font-weight: 300;
			font-size: 1.8vw;
		}

		&-price {
			padding-top: 5%;
			display: flex;
			justify-content: flex-end;
			align-self: center;
			font-weight: 400;
			text-align: right;
			font-size: 1.3vw;

			h4 {
				margin: 0;
				font-weight: 400;
				margin-right: 1%;
			}

			span {
				color: $gold;
			}
		}

		input {
			border: none;
			border-bottom: 1px solid $gold;
			background: transparent;
			outline: none;
			width: 60%;
			font-size: 2vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}
}
