.mafia-leaderboard {
	width: 95vw;
	height: 95vh;
	min-height: auto !important;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: url(../../../../images/italian-mafia_bg.jpg) no-repeat;
	border: 15px solid;
	border-image: url(../../../../images/factions/mafia/border.jpg);
	border-image-slice: 1% 1%;
	background-size: cover;
	background-position: center;
	padding: 1% 4%;
	color: #fff;

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-size: 1.5vw;
		color: #fff;
	}

	&_content {
		position: relative;
		min-height: 100%;

		&::before {
			@extend %pseudo;

			top: 5px;
			bottom: 0;
			left: 33%;
			width: 2px;
			background-color: #fff;
		}

		&::after {
			@extend %pseudo;

			top: 5px;
			bottom: 0;
			right: 33%;
			width: 2px;
			background-color: #fff;
		}
	}

	.header {
		margin-bottom: 1%;
		display: flex;
		justify-content: space-between;
		border-bottom: 2px solid #fff;

		h3 {
			margin: 0;
			width: 33%;
			text-align: center;
			font-size: 1.5vw;
			font-weight: 300;
			line-height: 2;
		}
	}

	.list {
		height: 75vh;
		overflow-y: scroll;
		padding: 0 1%;

		&::-webkit-scrollbar-track {
			background-color: #fff;
		}

		li {
			padding: 1% 0;
			display: flex;
			border-bottom: 2px solid #e5b030;

			p {
				margin: 0;
				width: 33%;
				font-size: 1.5vw;
				text-align: center;

				&:first-of-type {
					text-align: left;
				}
			}
		}
	}
}
