.player-death {
	@extend %window;

	background-image: url(../../../images/death_bg.jpg);
	width: 50%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_progress {
		margin-top: 3%;
		margin-bottom: 4%;
		border: 1px solid #fff;

		&-fill {
			padding: 1% 0;
			padding-left: 0;
			transition: width 0.3s ease;
			background-color: $gold;
			text-align: right;
			font-size: 1vw;
			font-weight: 300;
			color: $purple;
		}
	}

	&_medics {
		margin-bottom: 1%;
		text-align: right;
		font-size: 1.2vw;
		font-weight: 300;

		span {
			font-weight: 700;
			color: $gold;
		}
	}

	&_footer {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		button {
			width: 45%;
			font-size: 1.3vw;
		}
	}
}
