.barbershop {
	height: 100%;
	background: url(../../../images/bshop-bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	width: 28%;
	padding: 1%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-left: auto;

	.title {
		margin: 0 auto;
		padding-bottom: 5px;
		border-bottom: 1px solid $gold;
		font-weight: 300;
		font-size: 2vw;
		width: 80%;
		margin-bottom: 10px;
	}

	.controls {
		position: absolute;
		left: 30%;
		bottom: 3%;
		width: 15%;

		h4 {
			text-align: center;
			font-size: 1.5vw;
			font-weight: 200;
			margin: 0;
		}
	}

	&_container {
		padding-right: 4%;
		height: 80%;
		overflow-y: scroll;
	}

	&_footer {
		.buttons {
			padding-top: 4%;
			display: flex;
			justify-content: space-between;

			button {
				width: 49%;
			}
		}
	}
}
