.cityhall-passport {
	@extend %window;

	background-image: url(../../../images/cityhall/cityhall-bg.jpg);
	display: flex;
	flex-direction: column;
	width: 75%;
	padding: 1% 7% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_title {
		margin-bottom: 2%;
		width: 60%;
	}

	&_name {
		display: flex;
		align-items: center;
		padding-bottom: 5px;
		margin-bottom: 10px;

		h3 {
			text-align: left;
			font-weight: 300;
			margin: 0;
			font-size: 1.7vw;
		}

		span {
			padding-left: 20px;
			text-align: left;
			font-size: 2.2vw;
			font-weight: 700;
			color: $gold;
		}
	}

	&_form {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 30px;

		form {
			display: flex;
			justify-content: space-between;
			width: 95%;
		}

		h3 {
			font-size: 1.5vw;
			font-weight: 300;
			color: $gold;
		}

		input {
			width: 49.33%;
			padding: 1.5% 4%;
			border: 1px solid #fff;
			text-align: center;
			color: $purple;
			outline: none;
			font-family: inherit;
			font-weight: 400;
			font-size: 1.5vw;
			transition: all 0.3s ease;

			&::placeholder {
				color: $purple;
			}

			&:focus {
				background: transparent;
				color: #fff;
			}

			&:focus::placeholder {
				color: #fff;
			}
		}
	}

	&_info {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: 20px;

		div {
			width: 47%;
		}

		.remark {
			font-size: 1.2vw;
			line-height: 1.3;
			text-align: left;
			font-weight: 300;

			p {
				margin: 0;
				margin-bottom: 5px;
			}

			ul {
				@extend %list;

				li {
					position: relative;
					padding-left: 10px;

					&::before {
						@extend %pseudo;

						content: '-';
						left: 0;
					}
				}
			}

			strong {
				font-weight: 300;
				color: $gold;
			}
		}
	}

	&_price {
		padding-bottom: 10px;
		border-bottom: 1px solid $gold;

		h2 {
			font-size: 2vw;
			font-weight: 400;
			text-align: left;
			margin-top: 0;
			margin-bottom: 40px;
		}

		span {
			width: 65%;
			margin-left: auto;
			text-align: right;
			font-weight: 700;
			color: $gold;
			font-size: 2.2vw;
			border-top: 1px solid $gold;
			padding-top: 10px;
		}
	}

	&_footer {
		display: flex;
		justify-content: flex-end;
		width: 100%;

		button {
			width: 25%;
			margin-right: 5px;
		}
	}
}
