.gang-creator {
	@extend %window;

	width: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1% 3% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/gang-creator_bg.jpg);

	&_title {
		width: 70%;
	}

	form {
		padding-top: 5%;
		width: 100%;
		text-align: center;

		.error {
			position: absolute;
			top: 30%;
			left: 50%;
			transform: translateX(-50%);
			font-size: 1vw;
			color: $gold;
		}

		.remark {
			margin: 0 auto;
			text-align: center;
			font-size: 1.2vw;
		}
	}

	&_price {
		text-align: right;

		h4 {
			margin: 0;
			font-size: 1.3vw;
		}

		.price {
			font-size: 2.5vw;
			font-weight: 700;
			color: $gold;
		}
	}

	input {
		margin-bottom: 10%;
		width: 80%;
		background: none;
		border: none;
		outline: none;
		border-bottom: 1px solid $gold;
		color: #fff;
		font-family: inherit;
		font-size: 1.7vw;
		font-weight: 700;
		text-align: center;

		&::placeholder {
			color: #fff;
		}
	}

	.remark {
		font-size: 1.2vw;
		margin-bottom: 4%;
	}

	.buttons {
		padding-top: 5%;
		display: flex;
		justify-content: space-between;
		width: 100%;

		button[type='submit'] {
			background: $gold;
			border: 1px solid $gold;
			color: $purple;
		}
	}
}
