#badger {
	background-image: url(../../../images/phone/badger_bg.jpg);

	.badger {
		.title {
			margin-bottom: 10%;

			p {
				margin: 0;
				font-size: 1.3vw;
				font-weight: 300;
				line-height: 1.5;
			}

			img {
				display: block;
				width: 50%;
				height: 7vh;
			}
		}

		.content {
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		.phone-number {
			text-align: center;
			font-size: 1.3vw;
			font-weight: 300;

			span {
				display: block;
				font-size: 2.5vw;
				font-weight: 700;
			}
		}

		.btn-buy {
			@extend %button;

			margin-bottom: 6%;
			padding: 11% 6%;
			width: 80%;
			-webkit-mask-size: cover;
			-webkit-mask-repeat: no-repeat;
			-webkit-mask-image: url(../../../images/phone/badger_btn.svg);
			background: #fff;
			font-size: 1.5vw;
			font-weight: 300;
			color: #667de9;
			transition: opacity 0.3s ease;

			&:hover {
				opacity: 0.7;
			}

			&:disabled {
				opacity: 0.5;
			}
		}

		.price {
			font-size: 1.2vw;
		}

		&_controls {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			display: flex;

			button {
				@extend %button;

				padding: 10% 2%;
				width: 50%;
				font-size: 1.3vw;
				font-weight: 700;
				color: #fff;
				background: #667de9;
				transition: all 0.3s ease;

				&:first-of-type {
					border-right: 1px solid #764ba2;
				}

				&:hover {
					background: #ffffff;
					color: #667de9;
				}

				&.is-active {
					background: #ffffff;
					color: #667de9;
				}
			}
		}

		&_item--custom {
			form {
				padding-top: 10%;
				display: flex;
				flex-direction: column;
				align-items: center;
			}

			label {
				margin-bottom: 8%;
				text-align: center;
				font-size: 1.2vw;
				font-weight: 300;
				line-height: 2;

				input {
					padding: 2% 4%;
					background: transparent;
					outline: none;
					border-radius: 2vw;
					text-align: center;
					border: 1px solid #fff;
					font-family: inherit;
					font-size: 1.2vw;
					font-weight: 300;
					color: #fff;

					&::placeholder {
						color: #fff;
					}
				}
			}

			.price {
				margin-bottom: 8%;
			}
		}
	}
}
