.mafia-docs {
	background: url(../../../../images/mafia-docs.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	min-height: 87vh !important;
	position: absolute;
	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	.btn-exit {
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 2vw;
		color: #fff;
	}

	&_info {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		p {
			margin: 0;
			font-weight: 700;
			color: #e5b030;
			text-align: center;

			&.name {
				padding-bottom: 5%;
				font-size: 3.5vw;
				font-family: serif;
				font-weight: 300;
				position: relative;

				&::after {
					@extend %pseudo;

					background-color: #fff;
					height: 1px;
					width: 75%;
					left: 50%;
					transform: translateX(-50%);
					bottom: 0;
				}
			}

			&.rank {
				padding-top: 5%;
				font-size: 2.5vw;
				font-weight: 400;
			}
		}
	}
}

@media (max-width: 1024px) {
	.mafia-docs {
		width: 90%;
	}
}
