.admin_reports {
	&-list {
		height: 100vh;
		overflow-y: scroll;

		&::-webkit-scrollbar {
			width: 0.4vw;
		}
		&::-webkit-scrollbar-track {
			border: none;
			background: transparent;
		}
		&::-webkit-scrollbar-thumb {
			background: #fff;
		}
	}

	&-item {
		position: relative;
		padding: 1% 2%;
		padding-right: 6%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 0.1vh solid #0000;
		font-size: 1.2vw;
		font-weight: 300;
	}

	&-sender {
		width: 20%;
	}

	&-message {
		margin: 0 auto;
		width: 50%;
	}

	&-time {
		margin-left: auto;
	}

	&-reply {
		@extend %button;

		position: absolute;
		right: 1%;

		&:hover {
			opacity: 0.5;
		}

		svg {
			fill: $gold;
			width: 2vw;
			height: 2vw;
		}
	}
}
