.food-shop {
	@extend %window;

	width: 30%;
	bottom: 10%;
	left: 1.5%;
	background-image: url(../../../images/fshop-bg.jpg);
	padding: 1% 2%;
	color: #fff;

	&_header {
		margin-bottom: 4%;
	}

	.close-btn {
		position: absolute;
		top: 1%;
		left: 0;
		display: flex;
		align-items: center;
		color: #fff;

		&:hover span {
			border-bottom: 1px solid transparent;
		}

		svg {
			width: 1vw;
			height: 1vw;
			color: $gold;
		}

		span {
			margin-left: 1%;
			border-bottom: 1px solid #fff;
			font-size: 1vw;
			font-weight: 300;
		}
	}

	&_products {
		ul {
			@extend %list;

			margin-bottom: 4%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		li {
			margin-bottom: 1%;
			padding: 1%;
			width: 45%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #fff;

			.count {
				font-size: 2vw;
				font-weight: 300;
			}

			img {
				width: 3vw;
				height: 3vw;
				filter: invert(1);
			}
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;

		.price {
			margin-right: 8%;
		}
	}
}
