.gas-station {
	@extend %window;

	bottom: 3%;
	left: 1.5%;
	width: 30%;
	padding: 1% 2%;
	background-image: url(../../../images/gasstation-bg.jpg);
	color: #fff;

	&_info {
		margin-bottom: 15%;
		padding-top: 1%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.price {
			width: 50%;
			display: flex;
			align-items: center;

			span {
				padding-left: 1vw;
				font-size: 1.5vw;
				font-weight: 700;
				border-left: 2px solid $gold;
			}

			svg {
				width: 2vw;
				height: 2vw;
				color: $purple;
				background: $gold;
				padding: 2%;
				margin-right: 2%;
			}
		}

		.max-tank {
			font-size: 1.5vw;

			b {
				color: $gold;
			}
		}
	}

	&_form {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between;

		form {
			position: relative;
			width: 100%;
		}

		.count {
			width: 45%;
			position: relative;
			margin-bottom: 2%;
			padding-bottom: 1%;
			display: flex;
			border-bottom: 1px solid $gold;

			span {
				width: 100%;
				text-align: center;
				font-size: 1.5vw;
				font-weight: 700;
			}
		}

		&-liters {
			margin-bottom: 10%;

			.current {
				font-size: 1.3vw;
				padding: 0 2%;
			}
		}

		.count-controls {
			position: absolute;
			top: 15%;
			right: -12%;
		}

		.full-tank {
			position: absolute;
			top: 0;
			right: 0;
			font-size: 1.5vw;
		}

		&-jerrycans {
			margin-bottom: 15%;
			position: relative;

			.count {
				border-bottom: 1px solid #fff;
			}

			.count-controls {
				right: 2%;
				bottom: 0;
				justify-content: flex-end;
			}

			&::before {
				@extend %pseudo;

				width: 3vw;
				height: 3vw;
				left: 0;
				bottom: 8%;
				content: url(../../../images/jerrycan.svg);
				filter: invert(1);
			}
		}
	}

	&_footer {
		width: 70%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		margin-left: auto;

		.buttons {
			padding-top: 5%;
			display: flex;
			align-self: flex-end;

			button {
				margin-left: 1%;
			}
		}
	}
}
