.donation {
	background: url(../../../images/donation_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1% 2%;
	color: #fff;

	&_tab-list {
		ul {
			width: 100%;
			display: flex;
			border-bottom: 1px solid $gold;
		}

		li {
			padding: 0.5% 1%;
			font-size: 1.5vw;
			font-weight: 300;

			&.active {
				background: $gold;
				color: $purple;
			}
		}
	}

	&_tab {
		padding: 2% 1%;
		background: rgba(0, 0, 0, 0.15);
	}

	&_exit-btn {
		position: absolute;
		padding: 1% 2%;
		left: 2%;
		bottom: 2%;
	}

	&_balance {
		margin-bottom: 1%;
		padding-top: 1%;
		display: flex;
		align-items: center;
		justify-content: flex-end;

		&-title {
			margin: 0;
			padding-right: 1%;
			font-weight: 300;
		}

		&-item {
			font-weight: 700;
		}
	}

	&_services {
		&-list {
			padding: 2%;
			height: 60vh;
			overflow-y: scroll;
		}

		&-item {
			margin-bottom: 1%;
			display: flex;
			align-items: center;
			justify-content: space-between;
		}

		.info {
			padding: 0 2%;
			width: 80%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #fff;

			.name {
				margin: 0;
				font-size: 1.5vw;
			}
		}

		&-price {
			font-weight: 700;
			color: $gold;

			svg {
				fill: $gold !important;
			}
		}

		&-buy {
			width: 15%;
		}
	}
}
