}
.circle-menu_center {
  top: 50%;
  left: 50%;
  z-index: 1;
  width: 12vw;
  height: 12vw;
  background: linear-gradient(180deg, rgb(71, 23, 100) 50%, rgb(78, 19, 116) 100%);
  position: absolute;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border: 4px solid #f9c422;
  color: #f9c422;
  font-weight: 700;
  font-size: 1.3vw;
  outline: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.circle-menu_items {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
}
.circle-menu_item {
  top: 0;
  left: 0;
  width: 50%;
  height: 50%;
  cursor: pointer;
  position: absolute;
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
  background: rgba(52, 16, 74, 0.6);
  font-size: 4vw;
  font-weight: 300;
  color: #f9c422;
}
.circle-menu_item:hover {
  background: rgba(249, 196, 34, 0.7);
}
.circle-menu_item:hover .icon {
  color: #34104a;
  fill: #34104a;
}
.circle-menu_item:first-child {
  transform: rotate(-45deg) skew(45deg);
}
.circle-menu_item:first-child .icon {
  transform: skew(-45deg) rotate(45deg);
}
.circle-menu_item:nth-child(1) {
  transform: rotate(0deg) skew(45deg);
}
.circle-menu_item:nth-child(1) .icon {
  transform: skew(-45deg) rotate(0deg);
}
.circle-menu_item:nth-child(2) {
  transform: rotate(45deg) skew(45deg);
}
.circle-menu_item:nth-child(2) .icon {
  transform: skew(-45deg) rotate(-45deg);
}
.circle-menu_item:nth-child(3) {
  transform: rotate(90deg) skew(45deg);
}
.circle-menu_item:nth-child(3) .icon {
  transform: skew(-45deg) rotate(-90deg);
}
.circle-menu_item:nth-child(4) {
  transform: rotate(135deg) skew(45deg);
}
.circle-menu_item:nth-child(4) .icon {
  transform: skew(-45deg) rotate(-135deg);
}
.circle-menu_item:nth-child(5) {
  transform: rotate(180deg) skew(45deg);
}
.circle-menu_item:nth-child(5) .icon {
  transform: skew(-45deg) rotate(-180deg);
}
.circle-menu_item:nth-child(6) {
  transform: rotate(225deg) skew(45deg);
}
.circle-menu_item:nth-child(6) .icon {
  transform: skew(-45deg) rotate(-225deg);
}
.circle-menu_item:nth-child(7) {
  transform: rotate(270deg) skew(45deg);
}
.circle-menu_item:nth-child(7) .icon {
  transform: skew(-45deg) rotate(-270deg);
}
.circle-menu_item:nth-child(8) {
  transform: rotate(315deg) skew(45deg);
}
.circle-menu_item:nth-child(8) .icon {
  transform: skew(-45deg) rotate(-315deg);
}
.circle-menu .icon {
  width: 30%;
  height: 30%;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  position: absolute;
  fill: #f9c422;
}

