*,
*::after,
*::before {
  user-select: none;
  box-sizing: inherit;
}

html,
body {
  box-sizing: border-box;
  font-family: "HelveticaNeue";
  font-size: 1.3vw;
  line-height: 1.5;
  font-weight: 400;
  color: #fff;
}

body {
  margin: 0;
  padding: 0;
}

html,
body,
#root {
  height: 100%;
  background: transparent;
}

#root {
  position: relative;
  background: transparent;
  overflow-x: hidden;
}
#root > *:first-child {
  min-height: 100%;
}

::-webkit-scrollbar {
  width: 0.4vw;
}

::-webkit-scrollbar-track {
  background: #34104a;
}

::-webkit-scrollbar-thumb {
  background: #f9c422;
}

input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}