.job {
	@extend %window;

	background-image: url(../../../images/job-bg.jpg);
	width: 45%;
	padding: 1% 3% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_info {
		padding: 0 2%;
		text-align: left;
		font-size: 1.5vw;
		font-weight: 300;

		b {
			margin-left: 2%;
			color: $gold;
		}
	}

	.progress {
		margin-bottom: 4%;
		width: 100%;
		text-align: right;

		&_bar {
			border: 1px solid $gold;
			border-radius: 2vw;
			overflow: hidden;

			&-fill {
				transition: all 0.5s ease;
				height: inherit;
				background-color: $gold;
				height: 4vh;
			}
		}

		&_points {
			font-size: 1.3vw;
			font-weight: 300;
			line-height: 2;
			color: #fff;
		}
	}

	&_footer {
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
}
