.ems-pc {
	margin: 0 auto;
	padding: 2%;
	height: 100%;
	background: url(../../../../images/ems_bg.jpg) no-repeat;
	background-size: cover;

	&_info {
		display: flex;
		align-items: center;

		img {
			width: 6%;
			filter: invert(1);
		}

		&-text {
			padding-left: 2%;
			width: 50%;
			font-weight: 300;

			p {
				margin: 1% 0;
			}

			span {
				padding-left: 1%;
				font-weight: 700;
			}
		}
	}

	&_container {
		height: 75%;
		padding: 0 4% 2%;
	}

	&_list {
		padding-right: 2%;
		overflow-y: scroll;
		height: 100%;

		&::-webkit-scrollbar-track {
			background: #fff;
		}
		&::-webkit-scrollbar-thumb {
			background: #ca2020;
		}

		li {
			margin-bottom: 1%;
			padding: 1%;
			display: flex;
			justify-content: space-between;
			background: #fff;
			border-left: 6px solid #ed1c24;

			&:nth-child(even) {
				background: #fac8c8;
				border-left: 6px solid #f54249;
			}
		}

		p {
			margin: 0;
			color: #153d89;
		}

		button {
			background: #ca2020;
			font-size: 1.4vw;
			font-weight: 400;
			color: #fff;
			border: none;
			outline: none;
			transition: opacity 0.3s ease;

			&:hover {
				opacity: 0.7;
			}
		}
	}
}
