.mafia-offer {
	@extend %window;

	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: url(../../../../images/italian-mafia_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1% 4% 2%;
	color: #fff;

	p {
		padding: 0 7%;
		font-size: 1.3vw;
		font-weight: 300;
		text-align: center;
	}

	&_passport {
		padding: 0 4%;
		display: flex;
		justify-content: space-between;

		input {
			width: 45%;
			padding: 1% 2%;
			background: none;
			outline: none;
			border: 1px solid #fff;
			font-family: inherit;
			text-align: center;
			font-size: 1.3vw;
			color: #e5b030;

			&::placeholder {
				color: #e5b030;
			}
		}
	}

	&_footer {
		padding-top: 5%;

		.buttons {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}
	}

	&_price {
		margin: 0 auto 5%;
		width: 50%;
		font-size: 1.3vw;
		text-align: center;
		color: #e5b030;

		span {
			color: #fff;
			margin-left: 8%;
			font-size: 2vw;
			font-weight: 700;
		}
	}
}
