.vehicle-repair {
	@extend %window;

	padding: 2% 4%;
	width: 35%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/vrepair-bg.jpg);
	color: #fff;

	&_main {
		margin-top: 5%;
		padding-left: 1%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-left: 4px solid $gold;

		button {
			@extend %button;

			padding: 2%;
			width: 35%;
			border: 1px solid $gold;
			font-size: 1.5vw;
			color: $gold;
			text-align: left;
			transition: opacity 0.3s ease;

			&:hover {
				opacity: 0.5;
			}
		}

		span {
			font-size: 1.5vw;
			color: $gold;
		}
	}

	&_products {
		ul {
			margin-top: 7%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		li {
			margin-bottom: 1%;
			padding: 2%;
			width: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: space-between;

			.desc {
				width: 60%;
				display: flex;
				align-items: center;
				padding-bottom: 5px;
				border-bottom: 1px solid #fff;

				span {
					margin: 0;
					margin-left: 10px;
					padding-left: 10px;
					border-left: 1px solid $gold;
					font-size: 1.5vw;
				}
			}

			.count {
				width: 30%;
				display: flex;
				align-items: center;
				border-bottom: 1px solid #fff;

				span {
					margin: 0 auto;
					font-size: 1.5vw;
					line-height: 2.2;
				}
			}

			img {
				width: 3vw;
				height: 3vw;
				filter: invert(1);
			}
		}
	}

	&_price {
		margin-bottom: 6%;
		display: flex;
		align-items: center;
		justify-content: flex-end;

		h4 {
			margin: 0;
			font-weight: 400;
			font-size: 1.3vw;
			margin-right: 10px;
		}

		span {
			width: 70%;
			font-size: 1.7vw;
			border-bottom: 1px solid $gold;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}
}
