.cash-transfer {
	@extend %window;

	background-image: url(../../../images/givemoney-bg.jpg);
	width: 55%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_container {
		padding-top: 4%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 2%;
		width: 100%;

		h3 {
			margin: 0;
			margin-bottom: 1%;
			margin-left: auto;
			font-weight: 300;
			font-size: 1.6vw;
		}

		input {
			width: 100%;
			background: none;
			border: none;
			border-bottom: 2px solid #fff;
			color: $gold;
			font-family: inherit;
			font-weight: 700;
			font-size: 3vw;
			text-align: center;
			outline: none;
			margin-bottom: 20px;

			&::placeholder {
				color: #fff;
			}
		}
	}

	&_footer {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
