.hud_chat {
	width: 37%;
	position: relative;
	font-weight: 300;

	&.is-active {
		background: linear-gradient(to right, #363636, transparent);

		.hud_chat-messages {
			overflow-y: scroll;
		}
	}

	&-tabs {
		text-align: left;
		margin-bottom: 2%;
		position: relative;
		display: flex;

		&::before {
			@extend %pseudo;

			width: 100%;
			height: 1px;
			left: 0;
			bottom: 0;
			background: linear-gradient(to right, #fff, transparent 80%);
		}

		&_item {
			border: none;
			background: none;
			font-family: inherit;
			color: #a8a8a8;
			font-size: 1.2vw;
			outline: none;
			padding: 1% 2%;
			transition: all 0.3s ease;

			&.is-active {
				background: linear-gradient(to top, $purple, transparent 60%);
				color: $gold;
			}
		}
	}

	&-messages {
		@extend %list;

		display: flex;
		flex-direction: column;
		padding: 2%;
		padding-top: 0;
		margin-bottom: 2%;
		height: 22vh;
		overflow: hidden;
		opacity: 0.7;

		&.active {
			opacity: 1;
		}

		&::-webkit-scrollbar {
			width: 0.8%;
		}
		&::-webkit-scrollbar-track {
			box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
			background-color: transparent;
		}
		&::-webkit-scrollbar-thumb {
			background-color: $gold;
		}

		li {
			font-size: 1vw;
			text-align: left;
			letter-spacing: 0.05em;
			word-wrap: break-word;
			text-shadow: 0px 0px 2px black, 0px 0px 2px black, 0px 0px 2px black,
				0px 0px 2px black;
		}
	}

	&-form {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 2% 2% 1%;
		padding-right: 0;
		padding-left: 8%;
		position: relative;

		&::before {
			@extend %pseudo;

			width: 100%;
			height: 1px;
			left: 10px;
			top: 0;
			background: linear-gradient(to right, $gold, transparent 80%);
		}

		span {
			color: #a8a8a8;
			font-size: 1.2vw;
		}

		input {
			color: white;
			background: transparent;
			outline: none;
			border: 1px solid #a8a8a8;
			font-family: inherit;
			font-size: 1vw;
			width: 80%;
			padding: 0.5% 2%;
		}

		.micro-reload {
			@extend %button;
			position: absolute;
			left: 0px;

			svg {
				fill: $gold;
				width: 3vw;
				height: 3vw;
			}

			&:hover {
				opacity: 0.7;
			}
		}
	}
}

@media (max-width: 1024px) {
	.hud_chat {
		width: 40%;

		&-messages {
			height: 18vh;
		}
	}
}
