.clothing-shop {
	height: 100%;
	background: url(../../../images/clothshop_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	width: 25%;
	padding: 1%;
	margin-left: auto;

	&_params {
		padding: 1.5%;
		background: url(../../../images/clothshop-controls_bg.jpg) no-repeat;
		background-size: cover;
		background-position: center;
		position: absolute;
		left: 1%;
		bottom: 20%;
		width: 25%;

		&-item {
			display: flex;
			justify-content: space-between;
			padding-bottom: 5%;

			&:first-of-type {
				margin-top: 4%;
			}

			h6 {
				width: 22%;
				margin: 0;
				align-self: center;
				font-weight: 200;
				font-size: 1.3vw;
				margin-right: 2%;
			}
		}

		.controls {
			padding: 2%;
			width: 100%;

			h4 {
				font-size: 1.2vw;
				font-weight: 200;
				margin: 0;
				margin-top: 2%;
				text-align: left;
				padding-left: 2%;
			}
		}
	}

	&_list {
		margin-top: 10%;
		padding-right: 1%;
		height: 70vh;
		overflow-y: scroll;

		&-item {
			border: 1px solid #fff;
			padding: 1.5% 2%;
			margin-bottom: 2%;
			font-weight: 400;
			text-align: left;
			font-size: 1.2vw;
			transition: all 0.5s ease;

			&:hover {
				opacity: 0.7;
			}

			&.active {
				background: #fff;
				color: $purple;
				font-weight: 700;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	&_content {
		display: flex;
		min-height: 100%;
		flex-direction: column;
	}

	&_footer {
		margin-top: auto;
		border-top: 1px solid $gold;

		.buttons {
			padding-top: 5%;
			display: flex;
			justify-content: space-between;

			button {
				width: 49%;
			}
		}
	}
}
