.gold-miner {
	@extend %window;

	background-image: url(../../../images/goldMiner-bg.jpg);
	width: 50%;
	padding: 1% 2% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_title {
		width: 65%;
	}

	.remark {
		margin: 0;
		margin-bottom: 4%;
		margin-top: 5%;
		color: $gold;
		text-align: center;
		font-size: 1vw;
	}

	&_product {
		margin: 0 auto;
		width: 50%;
		margin-bottom: 5%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.icon {
			width: 4vw;
			height: 4vw;
			-webkit-mask-size: cover;
			mask-size: cover;
			-webkit-mask-repeat: no-repeat;
			background: $gold;
			margin-right: 15px;
			transition: all 0.3s ease;

			&-pickaxe {
				-webkit-mask-image: url(../../../images/pickaxe.svg);
			}
		}

		h3 {
			margin: 0;
			font-weight: 400;
			font-size: 2vw;
		}

		.price {
			font-weight: 700;
			color: $gold;
			font-size: 1.7vw;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
		width: 100%;
	}
}
