.car-wash {
	@extend %window;

	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 1% 5% 2%;
	width: 50%;
	background-image: url(../../..//images/garage-bg.jpg);
	background-size: cover;
	background-position: center;

	&_container {
		margin-bottom: 8%;
		padding: 0 8%;
	}

	&_price {
		padding-top: 5%;
		display: block;
		width: 100%;
		text-align: center;
		font-size: 3vw;
		font-weight: 700;
		color: $gold;
		border-bottom: 1px solid $gold;
	}

	&_footer {
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
}
