.tattoo {
	height: 100%;
	background: url(../../../images/tattoo-parlor_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	width: 28%;
	padding: 1%;
	margin-left: auto;
	display: flex;
	flex-direction: column;

	&_title {
		width: 80%;
	}

	.controls {
		position: absolute;
		left: 2%;
		bottom: 5%;
		width: 20%;

		h4 {
			font-size: 1.2vw;
			font-weight: 200;
			margin: 0;
			text-align: left;
			padding-left: 10px;
		}
	}

	&_params {
		margin-bottom: auto;
	}

	.selector {
		width: 70%;
	}

	&_params-item {
		display: flex;
		justify-content: space-between;
		padding-bottom: 5%;
		overflow: hidden;

		&:first-of-type {
			margin-top: 20px;
		}

		h6 {
			width: 25%;
			margin: 0;
			margin-right: 10%;
			align-self: center;
			font-weight: 200;
			font-size: 1.5vw;
			margin-right: 10px;
		}
	}

	.buttons {
		padding-top: 5%;
		display: flex;
		justify-content: space-between;

		button {
			width: 49%;
		}
	}
}
