@keyframes rotationAnimation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.main-preloader {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	background: linear-gradient(
		180deg,
		rgba(50, 3, 60, 1) 0%,
		rgba(52, 16, 74, 1) 28%,
		rgb(71, 16, 105) 100%
	);
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 5;

	svg {
		width: 10vw;
		height: 10vw;
		fill: $gold;
		animation: rotationAnimation 1s infinite linear;
	}
}
