.casino-slots {
	@extend %window;

	top: 15%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding: 1% 5%;
	padding-top: 3%;
	width: 50%;
	background-image: url(../../../../images/casino-slots_bg.jpg);
	background-size: cover;
	background-position: center;
	border-top: transparent;
	border-top-right-radius: 200px;
	border-bottom-right-radius: 200px;

	&_input {
		display: flex;
		align-items: center;

		span {
			font-size: 1.2vw;
			font-weight: 300;
		}

		input {
			margin-left: 6%;
			width: 100%;
			outline: none;
			background: none;
			border: none;
			color: #fff;
			font-family: inherit;
			font-size: 2vw;
			font-weight: 700;
		}
	}

	&_spin {
		@extend %button;

		position: absolute;
		top: 10%;
		right: 2%;
		width: 9vw;
		height: 9vw;
		background: $gold;
		border: 2px solid transparent;
		border-radius: 50%;
		font-size: 1.3vw;
		transition: all 0.3s ease-in-out;

		&:disabled {
			color: $purple;
		}

		&:hover {
			background: $purple;
			border-color: $gold;
			color: $gold;

			&:disabled {
				opacity: 1;
				background: $gold;
				color: $purple;
			}
		}
	}

	&_footer {
		padding-top: 2%;
		width: 100%;
		display: flex;
		align-items: center;
		border-top: 2px solid $gold;
	}

	&_info {
		padding-left: 10%;

		&-item {
			margin: 0;
			font-size: 1vw;
			font-weight: 300;

			.price {
				color: $gold;
			}
		}
	}
}
