div.notifications {
	margin-left: 0;
	padding: 0;
	max-height: 30vh;
	width: 35%;
	left: 50%;
	transform: translateX(-50%);

	&_item {
		position: relative;
		padding: 2%;
		max-height: 15vh;
		min-height: 8vh;
		overflow: visible;
		background: linear-gradient(
			180deg,
			rgba(50, 3, 60, 1) 0%,
			rgba(52, 16, 74, 1) 28%,
			rgb(71, 16, 105) 100%
		);
		border-radius: 0;
		text-align: center;
		font-family: inherit;
		font-size: 1.2vw;
		font-weight: 300;
		color: #fff;

		&::before {
			@extend %pseudo;

			top: 0;
			left: -3.5%;
			width: 0.8vw;
			height: 100%;
		}

		&.Toastify__toast {
			&--success::before {
				background: linear-gradient(
					180deg,
					rgba(23, 167, 71, 1) 0%,
					rgba(26, 184, 78, 1) 58%,
					rgba(23, 193, 79, 1) 100%
				);
			}

			&--info::before {
				background: linear-gradient(
					180deg,
					rgba(32, 132, 201, 1) 0%,
					rgba(45, 134, 199, 1) 58%,
					rgba(56, 139, 199, 1) 100%
				);
			}

			&--warning::before {
				background: linear-gradient(
					180deg,
					rgba(245, 192, 31, 1) 0%,
					rgba(244, 192, 35, 1) 58%,
					rgba(255, 198, 24, 1) 100%
				);
			}

			&--error::before {
				background: linear-gradient(
					180deg,
					rgba(201, 32, 32, 1) 0%,
					rgba(199, 45, 45, 1) 58%,
					rgba(199, 56, 56, 1) 100%
				);
			}
		}
	}

	&_progress {
		background: linear-gradient(
			90deg,
			rgba(245, 192, 31, 1) 0%,
			rgba(244, 192, 35, 1) 58%,
			rgba(255, 198, 24, 1) 100%
		);
	}

	&_body {
		display: block;
		text-align: center;
	}
}
