#ibonus {
	background: linear-gradient(
		180deg,
		rgba(50, 3, 60, 1) 0%,
		rgba(52, 16, 74, 1) 28%,
		rgba(85, 21, 124, 1) 100%
	);

	.ibonus {
		&_info {
			margin-bottom: 5%;
			border-bottom: 1px solid #fff;

			h2 {
				margin: 0 auto;
				padding-bottom: 2%;
				text-align: center;
				font-size: 2vw;
				color: $gold;
				border-bottom: 1px solid #fff;
			}

			p {
				margin: 2% 0;
				font-size: 0.9vw;
				font-weight: 300;

				b {
					color: $gold;
				}
			}
		}

		&_referals {
			h3 {
				margin: 0;
				margin-bottom: 2%;
				font-size: 1vw;
				font-weight: 400;
				white-space: nowrap;
			}

			&-item {
				margin-bottom: 2%;
				padding-left: 5%;
				font-size: 0.9vw;
				font-weight: 300;
				color: $gold;
			}
		}

		&_remark {
			margin: 0;
			font-size: 0.9vw;
			font-weight: 300;

			b {
				color: $gold;
			}
		}

		&_form {
			padding: 1vw 0;

			input {
				padding: 2% 4%;
				background: transparent;
				width: 100%;
				border: 1px solid #fff;
				color: #c7c7c7;
				font-size: 1vw;
				outline: none;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.7;
				}
			}

			button {
				padding: 0.7vw 1vw;
				width: 100%;
				font-size: 1.2vw;
			}
		}
	}
}
