.darknet {
	display: flex;
	background: url(../../../../images/darknet_bg.jpg) center bottom no-repeat;
	background-size: cover;

	.close-btn {
		@extend %button;

		font-size: 1vw;
		line-height: 2;
		text-align: left;
		color: #fff;
		border-bottom: 1px solid $pink;
	}

	&_wrapper {
		position: relative;
		width: 85%;
		align-self: center;
		display: flex;
		padding-top: 3.75%;
		padding-right: 2%;
		justify-content: space-between;
		margin: 0 auto;
		background: url(../../../../images/darknet-browser_bg.png) no-repeat;
		background-size: cover;
	}

	.security-pc {
		width: 100%;

		&_sidebar {
			width: 20%;
			padding-top: 4%;
			display: flex;
			flex-direction: column;
			background: rgb(18, 21, 23);
			position: relative;
		}

		&_content {
			width: 79%;
			padding-top: 2%;
		}

		&_footer {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0 4%;
			padding-bottom: 1%;

			.buttons {
				width: 31%;
				display: flex;
				justify-content: space-between;
			}

			button {
				border: 1px solid #ce0c0c;
			}
		}

		&_main {
			width: 100%;
			height: 80vh;

			background-color: rgba($color: #4b1e26, $alpha: 0.6);

			h2 {
				margin: 0;
			}

			.exit-btn {
				@extend %button;

				position: absolute;
				font-size: 1vw;
				line-height: 2;
				text-align: left;
				color: #fff;
				border-bottom: 1px solid $pink;
				opacity: 0;
				cursor: pointer;
				width: 2vw;
				height: 2vw;
				top: 0;
				right: 0;
			}

			.tabs {
				padding: 2%;
				height: inherit;
				display: flex;
				justify-content: space-between;

				&_item {
					width: 50%;
					display: flex;
					flex-direction: column;
					align-items: center;

					h2 {
						margin: 0;
						margin-bottom: 20%;
						font-size: 2.5vw;
						font-weight: 300;
					}

					button {
						@extend %button;

						width: 50%;
						padding: 3% 2%;
						font-size: 1.5vw;
						font-weight: 700;
						color: #fff;
						border-bottom: 2px solid $pink;
					}

					&--buy {
						border-right: 1px solid #fff;

						button {
							background: $pink;
						}
					}
				}
			}
		}

		&_weapons {
			display: flex;
		}

		&_money {
			padding-bottom: 1%;
			display: flex;
			flex-direction: column;
			position: relative;

			.header {
				width: 25%;
				padding: 0 2%;
				margin-bottom: 2%;
				align-self: flex-start;

				h2 {
					margin: 0 0 2%;
					font-size: 2.5vw;
					line-height: 1.7;
					font-weight: 400;
					text-align: right;
					border-bottom: 1px solid $pink;
				}

				button {
					@extend %button;

					display: block;
					font-size: 1.3vw;
					text-align: left;
					color: #fff;
				}
			}

			.balance {
				width: 50%;
				margin: 0 auto 4%;
				border-bottom: 1px solid $pink;

				span {
					display: block;
					font-size: 4.5vw;
					font-weight: 700;
					color: #fff;
					text-align: center;
				}
			}

			.members-list {
				@extend %list;

				position: absolute;
				width: 45%;
				min-height: 40vh;
				max-height: 50vh;
				padding: 2%;
				background: #141414;
				overflow-y: scroll;

				&::-webkit-scrollbar {
					width: 4px;
				}
				&::-webkit-scrollbar-track {
					background-color: rgba(0, 0, 0, 0.1);
				}
				&::-webkit-scrollbar-thumb {
					background-color: $gold;
				}

				li {
					padding: 1% 2%;
					display: flex;
					justify-content: space-between;
					border: 1px solid $pink;
					margin-bottom: 3px;
					font-size: 1.5vw;

					&:hover {
						background-color: $pink;
					}

					p {
						margin: 0;

						&.name {
							text-align: left;
						}

						&.rank {
							text-align: right;
						}
					}
				}
			}

			.payment-form {
				width: 50%;
				margin: 0 auto;

				form {
					display: flex;
					flex-direction: column;
					align-items: center;
				}

				input {
					width: 70%;
					margin-bottom: 2%;
					border: 1px solid #fff;
					outline: none;
					background: none;
					font-size: 1.5vw;
					line-height: 1.7;
					text-align: center;
					font-family: inherit;
					color: #fff;

					&::-webkit-outer-spin-button,
					&::-webkit-inner-spin-button {
						-webkit-appearance: none;
						margin: 0;
					}

					&::placeholder {
						color: #fff;
					}
				}

				button {
					@extend %button;

					width: 35%;
					background-color: $pink;
					border: 1px solid $pink;
					color: #fff;
					font-size: 1.3vw;
					padding: 1% 2%;
					transition: background-color 0.3s ease;

					&:hover {
						background-color: #261c1b;
					}

					&[type='submit'] {
						margin-top: 15%;
					}
				}

				p {
					width: 100%;
					font-size: 1.5vw;
					text-align: left;
				}
			}
		}
	}

	.categories {
		button {
			width: 100%;
			top: 0;
		}

		&_list {
			padding-top: 4%;
			margin-bottom: 8%;
		}

		&_item {
			background: $pink;
			margin-bottom: 4%;
			text-align: left;
			padding: 1% 2%;
			transition: all 0.5s ease;
			font-size: 1.2vw;
			font-weight: 300;

			&:hover {
				opacity: 0.5;
			}

			&.active {
				background: #fff;
				color: $pink;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	.ammo {
		margin-top: auto;
		padding: 0 1%;

		&_list {
			@extend %list;

			padding-right: 2%;
			border-left: 1px solid $pink;
		}

		&_item {
			display: flex;
			justify-content: space-between;
			padding-left: 4%;
			margin-bottom: 4%;

			input {
				width: 80%;
				border: 1px solid #fff;
				background: transparent;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 1.2vw;
				color: #fff;
			}

			span {
				width: 2vw;
				height: 2vw;
				margin-right: 1%;
				padding: 1%;
				border: 1px solid #fff;
			}

			svg {
				display: block;
				width: 1.5vw;
				height: 1.5vw;
				margin: 0 auto;
				fill: rgb(155, 155, 155);
			}
		}
	}

	.weapons_list {
		background-color: rgba($color: #4b1e26, $alpha: 0.6);
		height: 65vh;
		overflow-y: scroll;
		margin-bottom: 2%;

		&::-webkit-scrollbar {
			width: 4px;
		}
		&::-webkit-scrollbar-track {
			background-color: rgb(18, 21, 23);
		}
		&::-webkit-scrollbar-thumb {
			background-color: #ce0c0c;
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding: 0 4%;
			padding-top: 2%;
		}

		li {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			width: 30%;
			margin-right: 2%;
			margin-bottom: 4%;
			background: rgb(18, 21, 23);
			padding: 2%;
			padding-top: 4%;

			img {
				height: 3vw;
				width: 100%;
				padding: 2% 0;
				margin-bottom: 4%;
				filter: invert(1);
			}

			.price {
				font-size: 2vw;
			}

			.btn-buy {
				width: 40%;
				padding: 2% 3%;
				background: $pink;
				color: #fff;
				font-family: inherit;
				font-size: 1vw;
				border-radius: 2vw;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.5;
				}

				&.active {
					background: #fff;
					color: $pink;
					font-weight: 300;

					&:hover {
						opacity: 1;
					}
				}
			}
		}
	}
}
