.hud_online {
	position: absolute;
	top: 0;
	right: 0;
	padding: 0.5%;

	svg {
		width: 9vw;
		height: 2vw;
		filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.3));
	}

	&-container {
		margin-top: -2%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		text-align: center;
	}

	&-count {
		width: 45%;
		display: flex;
		align-items: center;

		span {
			font-size: 1vw;
			font-weight: 300;
			text-align: right;
			text-shadow: 0.1vw 0.1vw 0.1vw rgba(0, 0, 0, 0.2);
		}

		svg {
			width: 1.4vw;
			height: 1.4vw;
			fill: $gold;
		}
	}

	.player-id {
		margin: 0;
		width: 40%;
		font-size: 1vw;
		opacity: 0.5;
		text-align: right;
	}
}
