.ems-leaderboard {
	width: 90vw;
	min-height: auto !important;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: url(../../../../images/ems_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1% 4%;
	color: #fff;

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-size: 1.5vw;
		color: #fff;
	}

	&_logo {
		margin-bottom: 3%;
		display: flex;
		align-items: center;
		font-weight: 800;
		text-align: center;
		text-transform: uppercase;

		&-text {
			padding-left: 2%;
		}

		h1 {
			margin: 0;
			width: 80%;
			display: block;
			font-size: 2.3vw;
		}

		h2 {
			position: relative;
			margin: 0;
			font-size: 3.5vw;
		}

		img {
			width: 10%;
			display: block;
			min-height: auto;
			filter: invert(1);
		}
	}

	&_content {
		margin-bottom: 2%;
		padding: 1% 2%;
		background-color: rgba($color: #fff, $alpha: 0.3);
	}

	.header {
		margin-bottom: 1%;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #fff;

		h3 {
			margin: 0;
			width: 33%;
			text-align: center;
			font-size: 1.5vw;
			font-weight: 300;
			line-height: 2;

			&:first-of-type {
				text-align: left;
			}
		}
	}

	.list {
		height: 40vh;
		overflow-y: scroll;
		padding-right: 1%;

		&::-webkit-scrollbar-track {
			background-color: #5c0000;
		}

		&::-webkit-scrollbar-thumb {
			background: #fff;
		}

		li {
			margin-bottom: 1%;
			padding: 0.5% 0;
			display: flex;
			border: 1px solid #fff;

			p {
				margin: 0;
				width: 33%;
				font-size: 1.5vw;
				font-weight: 700;
				color: #fff;
				text-align: center;

				&:first-of-type {
					padding-left: 5%;
					text-align: left;
				}
			}
		}
	}
}
