.player-licenses {
	background: url(../../../images/licenses-card.jpg) no-repeat;
	background-size: cover;
	background-position: top center;
	min-height: auto !important;
	display: flex;
	flex-direction: column;
	position: absolute;
	width: 60%;
	padding: 18% 26% 4% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-weight: 700;
		font-size: 1.7vw;
		color: #2e1a0b;
	}

	&_items {
		height: 35vh;
		overflow-y: scroll;
		padding-right: 15px;

		&::-webkit-scrollbar {
			width: 4px;
		}
		&::-webkit-scrollbar-track {
			box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
			background-color: transparent;
		}
		&::-webkit-scrollbar-thumb {
			background-color: rgb(95, 61, 35);
		}

		li {
			text-align: left;
			color: #2e1a0b;
			font-weight: 700;
			line-height: 1.7;
			border-bottom: 1px solid #2e1a0b;
			font-size: 1.4vw;
			margin-bottom: 3%;
		}
	}
}

@media (max-width: 1024px) {
	.player-licenses {
		width: 70%;
	}
}
