.security-docs {
	background: url(../../../../images/security-card.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	min-height: 85vh !important;
	position: absolute;
	width: 65%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	.btn-exit {
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 2vw;
		color: #fff;
	}

	p {
		margin: 0;
	}

	.name {
		position: absolute;
		left: 15%;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 4vw;
		font-family: 'BebasNeue';
	}

	.rank {
		position: absolute;
		bottom: 10px;
		left: 15%;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 8vw;
		color: #9d0000;
		font-family: 'BebasNeue';
	}
}

@media (max-width: 1024px) {
	.security-docs {
		width: 90%;
	}
}
