.char {
	&_container {
		width: 25%;
		height: 100vh;
		overflow-y: scroll;
		background: url(../../../images/sidebar-bg.jpg) center no-repeat;
		background-size: cover;
		padding: 0 2% 2%;
	}

	&.plastic {
		.buttons {
			padding-top: 4%;
			display: flex;
			justify-content: space-between;
		}
	}

	&_item {
		margin-top: 5%;

		&-hair {
			.selector {
				width: 50%;
			}
		}
	}

	&_gender {
		display: flex;
		justify-content: center;
		margin-bottom: 10%;

		label:first-of-type {
			margin-right: 10%;
		}

		input {
			display: none;
		}

		span {
			font-family: 'FuturicaBook';
			font-size: 1.2vw;
			color: #a8a8a8;
			transition: color 0.2s ease;
		}

		input:checked ~ span {
			color: $gold;
		}
	}

	&_row {
		margin-top: 6%;

		h6 {
			margin: 0;
			align-self: center;
			font-weight: 200;
			font-size: 1.2vw;
			width: 40%;
		}

		h5 {
			margin: 0;
			font-weight: 200;
			font-size: 1.1vw;
			color: $gold;
			text-align: left;
			padding-left: 2.5%;
		}
	}

	&_content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-bottom: 5%;
	}

	.controls {
		position: absolute;
		left: 50%;
		bottom: 3%;
		width: 15%;

		h4 {
			text-align: center;
			font-size: 1.5vw;
			font-weight: 200;
			margin: 0;
		}
	}

	&_submit {
		position: absolute;
		right: 3%;
		bottom: 3%;
	}
}

.hair {
	.char_content div {
		width: 50%;
	}

	.char_row h6 {
		text-align: left;
	}
}
