.police-docs {
	background: url(../../../../images/police-badge.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	min-height: 80vh !important;
	position: absolute;
	width: 40%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;

	.btn-exit {
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 2vw;
		color: $gold;
	}

	&_info {
		position: absolute;
		top: 45%;
		left: 50%;
		transform: translate(-50%, -50%);

		p {
			margin: 0;
			font-weight: 700;

			&.name {
				font-size: 1.3vw;
			}

			&.rank {
				font-size: 2vw;
			}
		}
	}
}

@media (max-width: 1024px) {
	.police-docs {
		width: 50%;

		&_info {
			p {
				margin: 0;
				font-weight: 700;

				&.name {
					font-size: 1.5vw;
				}

				&.rank {
					font-size: 2.5vw;
				}
			}
		}
	}
}
