.weapon-shop {
	display: flex;
	justify-content: space-between;
	background: url(../../../images/wshop-bg.jpg) no-repeat;
	background-size: cover;
	padding: 1% 3%;

	&_categories {
		width: 20%;

		&-list {
			padding-top: 5%;
		}

		li {
			border: 1px solid #fff;
			margin-bottom: 4%;
			text-align: left;
			padding: 2% 4%;
			transition: all 0.5s ease;
			font-size: 1.2vw;

			&:hover {
				opacity: 0.5;
			}

			&.is-active {
				background: #fff;
				color: $purple;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	&_container {
		padding-top: 3.2%;
		width: 79%;
	}

	&_list {
		margin-bottom: 4%;
		height: 65vh;
		background-color: rgba($color: #000000, $alpha: 0.25);
		overflow-y: scroll;

		ul {
			padding: 0 4%;
			padding-top: 2%;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
		}

		li {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 18%;
			margin-right: 2%;
			margin-bottom: 4%;

			img {
				width: 100%;
				height: 10vh;
				padding-bottom: 8%;
				border-bottom: 1px solid $gold;
			}

			.price {
				padding: 4%;
				font-size: 1.6vw;
			}

			button {
				width: 100%;
				padding: 3% 8%;
			}

			&.active button {
				background: #fff;
				color: $purple;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	&_ammo {
		width: 30%;

		&-form {
			padding-top: 4%;
			padding-left: 10%;
			display: flex;

			.titles {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				border-right: 1px solid $gold;
				padding-right: 4%;

				h5 {
					margin: 0;
					font-weight: 200;
					font-size: 1vw;
					line-height: 1.5;
					text-align: right;
				}
			}

			input {
				margin-top: 4%;
				border: 1px solid #fff;
				padding: 2% 4%;
				color: #fff;
				text-align: center;
				outline: none;
				font-family: inherit;
				background: transparent;
				font-size: 1.1vw;

				&::-webkit-outer-spin-button,
				&::-webkit-inner-spin-button {
					-webkit-appearance: none;
					margin: 0;
				}
			}
		}

		&-items {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: flex-start;
			padding-left: 4%;

			ul {
				display: flex;
			}

			li {
				width: 2vw;
				height: 2vw;
				margin-right: 4%;
				padding: 1%;
				border: 1px solid #fff;

				&.active {
					background: #fff;

					svg {
						fill: #330f48;
					}
				}

				svg {
					width: 1.7vw;
					height: 1.7vw;
					fill: rgb(155, 155, 155);
				}
			}
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}

	&_price {
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		.buttons {
			display: flex;

			button {
				&:first-child {
					margin-right: 5px;
				}
			}
		}
	}
}
