%button {
	border: none;
	padding: 0;
	background: transparent;
	outline: none;
	font-family: inherit;
}

%list {
	padding: 0;
	margin: 0;
	list-style: none;
}

%pseudo {
	position: absolute;
	content: '';
}

%window {
	position: absolute;
	min-height: auto !important;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	border-top: 0.4vw solid $gold;
	box-shadow: 0px 4px 4px 4px rgba(0, 0, 0, 0.2);
}
