.alert-enter {
	opacity: 0;
	transform: scale(0.98);
}
.alert-enter-active {
	opacity: 1;
	transform: translateX(0);
	transition: opacity 300ms, transform 300ms;
}
.alert-exit {
	opacity: 1;
}
.alert-exit-active {
	opacity: 0;
	transform: scale(0.98);
	transition: opacity 300ms, transform 300ms;
}

.slideLeft-enter {
	opacity: 0;
	transform: translate3d(-98%, 0, 0);
}
.slideLeft-enter-active {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: opacity 300ms, transform 300ms;
}
.slideLeft-exit {
	opacity: 0.8;
}
.slideLeft-exit-active {
	opacity: 0;
	transform: translate3d(-98%, 0, 0);
	transition: opacity 300ms, transform 280ms;
}

.slideUp-enter {
	opacity: 0;
	transform: translate3d(0, 98%, 0);
}
.slideUp-enter-active {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: opacity 300ms, transform 300ms;
}
.slideUp-exit {
	opacity: 0.8;
}
.slideUp-exit-active {
	opacity: 0;
	transform: translate3d(0, 98%, 0);
	transition: opacity 300ms, transform 280ms;
}
