.faction-leader {
	@extend %window;

	width: 65%;
	padding: 1% 3% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/factions/leader-bg.jpg);
	font-weight: 300;

	&_container {
		padding-top: 3%;
		display: flex;
		justify-content: space-between;
	}

	&_title {
		margin-bottom: 2%;
	}

	&_sidebar {
		width: 20%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	&_tabs {
		display: flex;
		flex-direction: column;

		&-item {
			margin-bottom: 10%;
			padding: 6%;
			width: 100%;
		}

		.active {
			background: $gold;
			border: 1px solid $gold;
			color: $purple;
		}
	}

	&_tab {
		width: 75%;
		background: rgba($color: #000, $alpha: 0.25);
		padding: 2%;
		height: 55vh;
		overflow-y: scroll;
	}

	&_invite {
		li {
			display: flex;
			justify-content: space-between;
			background: #3f2457;
			padding: 2%;

			&:nth-child(even) {
				background: $purple;
			}

			.name {
				margin: 0;
				font-size: 1.5vw;
				color: $gold;
			}

			button {
				@extend %button;

				color: #fff;
				font-size: 1.3vw;
				font-family: inherit;
			}
		}
	}

	&_members {
		li {
			display: flex;
			align-items: center;
			justify-content: space-between;
			background: #3f2457;
			padding: 2%;
			opacity: 0.7;

			&.online {
				opacity: 1;
			}

			&:nth-child(even) {
				background: $purple;
			}

			.info {
				text-align: left;
			}

			.name {
				margin: 0;
				margin-bottom: 1%;
				font-size: 1.5vw;
				color: $gold;
			}

			.rank {
				margin: 0;
				font-size: 1.3vw;
			}

			.buttons {
				width: 30%;
				display: flex;
				justify-content: space-between;
			}

			button {
				@extend %button;

				margin-left: 1%;
				color: #fff;
				font-size: 1.3vw;
				font-family: inherit;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.5;
				}
			}
		}
	}

	&_info {
		li {
			text-align: left;
			padding-left: 2%;
			border-left: 2px solid $gold;
			margin-bottom: 4%;

			.rank {
				font-size: 1.7vw;
				color: $gold;
				margin: 0;
				margin-bottom: 2%;
			}

			p {
				font-size: 1.3vw;
				margin: 0;
				margin-bottom: 2%;

				span {
					color: $gold;
				}
			}
		}
	}

	&_modal {
		display: flex;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0, 0, 0, 0.5);

		&-container {
			padding: 2% 5%;
			border-top: 5px solid $gold;
			background: url(../../../images/factions/leader-bg.jpg) no-repeat;
			background-size: cover;
			width: 50%;
			margin: auto;
		}

		p {
			font-size: 1.3vw;
			margin: 0;
			margin-bottom: 1%;
			text-align: left;

			span {
				color: $gold;
				font-weight: 700;
			}
		}

		.name {
			color: $gold;
		}

		.rank-select {
			display: flex;
			align-items: center;
			margin-bottom: 20px;

			&_current {
				width: 100%;
				font-size: 2vw;
				font-weight: 700;
				line-height: 1.2;
				color: $gold;
				border-bottom: 1px solid $gold;
			}

			&_controls {
				display: flex;
				flex-direction: column;
				margin-left: 5px;

				&-item {
					@extend %button;

					font-size: 1.7vw;
					color: $gold;
				}
			}
		}

		.buttons {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: center;

			button {
				width: 48%;
			}
		}
	}
}

@media (max-width: 1024px) {
	.faction-leader {
		width: 70%;
	}
}
