#vehicles {
	background: #fff;
	text-align: left;
	color: #000;

	.phone_status-bar {
		color: #000;
	}

	.phone_content {
		padding: 0;
	}

	.vehicles {
		padding-top: 14%;
		color: #000;

		&_header {
			padding: 0 4%;
		}

		.title {
			margin: 0;
			margin-bottom: 4%;
			font-size: 1.7vw;
		}

		&_search input {
			background: #edeef0;
			color: #000;
		}

		&_list {
			padding-bottom: 4%;
			height: 25vw;
			overflow-y: scroll;

			&::-webkit-scrollbar {
				display: none;
			}

			li {
				padding: 2% 1%;
				display: flex;
				align-items: center;
				justify-content: space-between;
				text-align: left;
				font-size: 1.1vw;
				font-weight: 700;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.6;
				}

				.name {
					margin: 0;
					padding-left: 4%;
					width: 100%;
					line-height: 2;
					border-bottom: 1px solid #e6e6e6;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}

		&_vehicle-info {
			margin-bottom: 8%;
		}
	}
}
