.hud_ammo {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	position: absolute;
	right: 0;
	top: -52%;
	width: 18%;

	&-icon {
		width: 3vw;
		height: 3vw;
		color: $gold;
	}

	&-count {
		padding-left: 2%;
		font-size: 1vw;
		font-weight: 300;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
	}
}
