.register {
	.auth {
		&_container {
			padding: 8% 2% 1.8% 18%;
			background-image: url(../../../images/register_bg.png);

			svg {
				margin-left: auto;
			}
		}

		&_form {
			label {
				display: block;
				margin-bottom: 6%;
			}

			div {
				display: flex;
				width: 70%;
				justify-content: space-between;

				label {
					width: 49%;
				}
			}

			.transparent_input {
				padding: 2% 4%;
				background: transparent;
				width: 100%;
				border: 1px solid #fff;
				color: #c7c7c7;
				font-family: 'FuturicaBook';
				font-size: 1.6vw;
				outline: none;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.7;
				}
			}
		}
	}
}

@media (max-width: 1024px) {
	.register {
		.auth {
			&_container {
				padding-top: 12%;
			}

			&_form button {
				margin-left: -5%;
				padding: 2% 5%;
			}
		}
	}
}
