.mafia-pc {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	height: 100%;
	margin: 0 auto;
	padding: 2% 0;
	background: url(../../../../images/italian-mafia_bg.jpg) no-repeat;
	background-size: cover;

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-size: 2vw;
		color: #fff;
	}

	&_tab-list {
		padding: 2% 4%;
	}

	&_tab {
		width: 100%;
		display: flex;
		flex-direction: column;
		position: relative;
	}

	&_money {
		.header {
			width: 25%;
			padding: 0 2%;
			margin-bottom: 2%;
			align-self: flex-start;

			h2 {
				margin: 0 0 2%;
				font-size: 2.5vw;
				line-height: 1.7;
				font-weight: 300;
				text-align: right;
				border-bottom: 2px solid #e5b030;
			}

			button {
				@extend %button;

				display: block;
				font-size: 1.3vw;
				font-weight: 300;
				text-align: left;
				color: #fff;
			}
		}

		.balance {
			margin-bottom: 4%;
			border-top: 1px solid #fff;
			border-bottom: 1px solid #fff;
			background: #181818;

			span {
				display: block;
				text-align: center;
				font-size: 5vw;
				font-weight: 200;
				color: #e5b030;
			}
		}

		.members-list {
			position: absolute;
			width: 45%;
			min-height: 40vh;
			max-height: 50vh;
			padding: 2%;
			background: #141414;
			overflow-y: scroll;

			&::-webkit-scrollbar {
				width: 4px;
			}
			&::-webkit-scrollbar-track {
				background-color: rgba(0, 0, 0, 0.1);
			}
			&::-webkit-scrollbar-thumb {
				background-color: $gold;
			}

			li {
				padding: 1% 2%;
				display: flex;
				justify-content: space-between;
				border: 1px solid #e5b030;
				margin-bottom: 3px;
				font-size: 1.5vw;

				&:hover {
					background-color: #e5b030;
				}

				p {
					margin: 0;

					&.name {
						text-align: left;
					}

					&.rank {
						text-align: right;
					}
				}
			}
		}

		.payment-form {
			width: 50%;
			margin: 0 auto;

			form {
				display: flex;
				flex-direction: column;
				align-items: center;
			}

			input {
				width: 70%;
				margin-bottom: 2%;
				border: none;
				outline: none;
				border-bottom: 2px solid #e5b030;
				background: none;
				font-size: 1.5vw;
				line-height: 1.7;
				text-align: center;
				font-family: inherit;
				color: #fff;

				&::placeholder {
					color: #fff;
				}
			}

			button {
				padding: 1% 2%;
				width: 35%;
				background-color: #e5b030;
				border: 1px solid #e5b030;
				color: #fff;
				font-size: 1.3vw;
				transition: background-color 0.3s ease;

				&:hover {
					background-color: #181818;
				}

				&[type='submit'] {
					margin-top: 15%;
				}
			}

			p {
				width: 100%;
				font-size: 1.5vw;
				font-weight: 300;
				text-align: left;
			}
		}
	}
}
