#forbes {
	background: #1b1a21;
	text-align: left;

	.promo {
		margin-bottom: 20%;

		h1 {
			margin: 0 auto;
			width: 75%;
			font-size: 3.3vw;
			font-family: serif;
			text-align: center;
			border-bottom: 1px solid #d39030;
		}

		p {
			font-size: 1.3vw;
			text-align: center;
		}
	}

	.forbes_list {
		padding: 0 3%;

		li {
			margin-bottom: 15%;
			font-size: 1.5vw;
			font-family: serif;
			color: #d39030;

			.name {
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}

			.name span {
				padding: 1%;
			}

			&:first-child {
				.name span {
					color: #1b1a21;
					background-color: #d39030;
				}
			}
		}
	}
}
