.police-pc {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	height: 100%;
	margin: 0 auto;
	padding: 2%;
	padding-right: 4%;
	padding-top: 10%;
	background: url(../../../../images/police_bg.jpg) no-repeat;
	background-size: cover;

	&_logo {
		top: 2%;
		position: absolute;
		display: flex;
		width: 100%;
		align-items: center;
		padding-left: 2%;

		h1 {
			font-size: 2vw;
			margin-left: 2%;
		}

		img {
			display: block;
			width: 6vw;
			height: 6.5vw;
		}
	}

	&_user {
		position: absolute;
		top: 2%;
		right: 4%;
		text-align: right;

		h3 {
			font-size: 1.5vw;
			font-weight: 300;
			margin: 0;
		}

		p {
			font-weight: 700;
			color: $gold;
			margin: 0;
		}
	}

	&_violators {
		width: 100%;
		display: flex;
		flex-direction: column;
		padding: 0 4%;

		.search-fields {
			margin-left: auto;
			display: flex;
		}

		input {
			margin-left: 1%;
			width: 50%;
			font-size: 1vw;
			font-weight: 700;
			font-family: inherit;
			border: none;
			outline: none;
			color: #1a3f84;
			padding: 2% 4%;
			text-align: right;

			&::placeholder {
				font-weight: 300;
				color: #1a3f84;
			}
		}

		&-list {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding-top: 2%;
			margin-bottom: 1%;
		}

		&-item {
			display: flex;
			background: #fff;
			width: 100%;
			border-left: 6px solid $gold;
			padding: 1%;
			margin-bottom: 1%;

			&:nth-child(even) {
				background: #faedc8;
			}

			div {
				width: 33%;

				&:not(:last-of-type) {
					margin-right: 2%;
				}
			}

			h3 {
				margin: 0;
				margin-bottom: 1%;
				font-size: 1.2vw;
				font-weight: 300;
				text-align: left;
				color: #1a3f84;
			}

			p {
				margin: 0;
				padding: 1% 2%;
				background: #1a3f84;
				font-size: 1.3vw;
				font-weight: 700;
				color: $gold;
			}
		}
	}

	&_content {
		padding-top: 1%;
		width: 75%;
		height: 100%;
		border-top: 2px solid $gold;
		background-color: rgba($color: #000000, $alpha: 0.25);
		overflow-y: scroll;

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.1);
		}
	}

	&_sidebar {
		width: 20%;
		display: flex;
		flex-direction: column;
		min-height: 100%;

		img {
			width: 30%;
		}

		button {
			margin-top: auto;
		}
	}

	&_tab-list {
		li {
			margin-bottom: 3%;
			text-align: left;
			padding: 2% 4%;
			transition: all 0.5s ease;
			font-size: 1.5vw;
			color: $gold;
			border: 1px solid $gold;

			&:hover {
				opacity: 0.5;
			}

			&.active {
				background: $gold;
				color: #1a3f84;
				font-weight: 700;

				&:hover {
					opacity: 1;
				}
			}
		}
	}

	&_vehicle-info {
		display: flex;
		flex-direction: column;
		min-height: 100%;

		form {
			display: flex;
			flex-direction: column;
			width: 50%;
			margin: 0 auto;
			margin-bottom: 4%;

			p {
				font-weight: 700;
				font-size: 1.3vw;
				text-align: left;
				margin-bottom: 2%;
			}

			input {
				width: 100%;
				margin-bottom: 10px;
				border: none;
				border-left: 4px solid $gold;
				padding: 5px 15px;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 2vw;
				font-weight: 700;
				color: #1a3f84;
			}

			button {
				margin-left: auto;
			}
		}

		h2 {
			width: 60%;
			margin: 0 auto;
			margin-top: auto;
			font-size: 1.7vw;
			color: $gold;
			padding-bottom: 5px;
			border-bottom: 1px solid #fff;
		}

		.list {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: auto;
			padding: 20px;
			background-color: rgba($color: #000000, $alpha: 0.25);
			min-height: 30vh;
			text-align: left;

			ul {
				width: 100%;
			}

			p {
				font-size: 1.3vw;
				font-weight: 300;

				b {
					color: $gold;
				}
			}
		}
	}

	&_licenses {
		padding: 1% 5%;

		form {
			margin-bottom: 2%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			input {
				width: 30%;
				border: none;
				padding: 1% 2%;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 2vw;
				font-weight: 700;
				color: #1a3f84;

				&::placeholder {
					color: #1a3f84;
				}
			}
		}

		.list {
			min-height: 30vh;
			padding: 0 4%;
			background-color: rgba($color: #000000, $alpha: 0.25);

			.header {
				margin-bottom: 2%;
				padding: 0 4%;
				display: flex;
				justify-content: space-between;
				border-bottom: 1px solid $gold;

				p {
					margin: 0;
					font-size: 1.2vw;
					font-weight: 700;
					line-height: 2;
					color: $gold;

					&:first-child {
						width: 70%;
						border-right: 1px solid $gold;
					}
				}
			}

			li {
				display: flex;
				justify-content: space-between;
				background: #fff;
				width: 100%;
				border-left: 6px solid $gold;
				padding: 0 1%;
				margin-bottom: 2px;

				&:nth-child(even) {
					background: #faedc8;
				}

				div {
					padding: 1% 0;
					width: 33%;

					&:first-of-type {
						width: 70%;
						border-right: 1px solid #1a3f84;
					}

					&:last-of-type {
						border-left: 6px solid $gold;
					}
				}

				p {
					margin: 0;
					padding: 2px 5px;
					font-size: 1.3vw;
					font-weight: 700;
					text-align: left;
					color: #1a3f84;
				}
			}
		}
	}
}
