.inventory {
	@extend %window;

	display: flex;
	flex-direction: column;
	width: 85%;
	padding: 1% 2% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/inventory-bg.jpg);

	&--double {
		.inventory {
			&_items {
				width: 100%;
			}
		}
	}

	&_container {
		margin-bottom: 2%;
		padding: 2% 2% 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&_title {
		width: 60%;
	}

	&_clothes {
		display: flex;
		width: 27%;
		justify-content: space-between;
		align-items: center;

		&.male {
			ul {
				background: url(../../../images/male.svg) 57% center no-repeat;
				background-size: cover;
			}
		}

		&.female {
			ul {
				background: url(../../../images/female.svg) 55% center no-repeat;
				background-size: cover;
			}
		}

		ul {
			width: 100%;
			display: flex;
			align-items: flex-end;
			justify-content: space-between;
			flex-wrap: wrap;
			flex-direction: column;
			height: 25vw;

			li {
				position: relative;
				width: 5vw;
				height: 5vw;
				display: flex;
				justify-content: center;
				align-items: center;
				border: 1px solid $gold;
				text-align: center;
				line-height: 5vw;
				font-size: 1vw;
				margin-bottom: 0.5%;

				img {
					width: 3vw;
					height: 3vw;
					filter: invert(1);
				}

				&:nth-child(-n + 4) {
					margin-right: auto;
				}

				&.active {
					span {
						font-size: 0;
					}
				}
			}
		}
	}

	&_slots {
		font-size: 1.3vw;

		b {
			font-weight: 700;
			color: $gold;
		}
	}

	&_others {
		margin-left: 1%;
		display: flex;
		flex-direction: column;
		width: 17%;
		text-align: center;
		font-weight: 300;

		&-item {
			height: 4vw;
			line-height: 4vw;
			border: 1px solid $gold;
			margin-bottom: 4%;

			&.active span {
				display: block;
				font-size: 0;
				background-position: center;
				background-repeat: no-repeat;
				background-size: 80% 80%;
				filter: invert(1);
			}

			img {
				width: 6vw;
				height: 4vw;
				filter: invert(1);
			}
		}
	}

	&_items {
		margin-left: 1%;
		padding: 1%;
		width: 50%;
		background: rgba(0, 0, 0, 0.25);

		&-container {
			width: 49%;
		}

		&-header {
			padding: 0 2%;
			display: flex;
			justify-content: space-between;
			padding-bottom: 5px;
			border-bottom: 1px solid $gold;
			margin-bottom: 2%;

			h3 {
				margin: 0;
				text-align: center;
				font-weight: 300;
				font-size: 1.3vw;

				&:nth-child(2) {
					width: 30%;
					border-left: 1px solid $gold;
				}
			}
		}

		&-list {
			position: relative;
			height: 50vh;
			padding: 2% 3%;
			overflow: hidden;
			overflow-y: scroll;
		}

		&-item {
			width: 100%;
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
			padding-bottom: 2%;
			color: #fff;

			&--preview {
				top: -10% !important;
				left: -10% !important;
				width: 43%;
			}

			&.is-active {
				color: $gold;
			}

			.icon {
				width: 70%;
				padding-bottom: 5px;
				border-bottom: 1px solid currentColor;

				img {
					display: block;
					margin: 0 auto;
					width: 3vw;
					height: 3vw;
					filter: invert(1);
				}
			}

			.count {
				width: 25%;
				font-size: 1.3vw;
				line-height: 2;
				text-align: center;
				color: #fff;
				border-bottom: 1px solid currentColor;
			}
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;

		button {
			font-size: 1.5vw;
		}
	}

	&_controls {
		width: 50%;
		display: flex;
		justify-content: flex-end;

		&-item {
			margin-left: 2%;
		}
	}

	.modal {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0, 0, 0, 0.5);

		&_container {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			padding: 2% 5%;
			display: flex;
			flex-direction: column;
			border-top: 5px solid $gold;
			background: url(../../../images/factions/leader-bg.jpg) no-repeat;
			background-size: cover;
			width: 55%;
		}

		&_title {
			margin-bottom: 4%;
			width: 85%;

			span {
				text-transform: lowercase;
				color: $gold;
			}
		}

		&_slider {
			padding: 2% 0;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		.buttons {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}
	}
}

@media (max-width: 1024px) {
	.inventory {
		&_clothes {
			ul {
				height: 34vh;
			}
		}

		&_items {
			ul {
				height: 34vh;
			}
		}
	}
}
