@keyframes flickerAnimation {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.speedometer {
	background-size: cover;
	position: absolute;
	min-width: 15%;
	max-width: 20%;
	right: 1%;
	bottom: 25%;
	padding: 1.5% 0.5%;
	padding-right: 0;
	opacity: 0.7;
	transition: opacity 0.5s ease;

	&.active {
		opacity: 1;
	}

	.speed {
		position: absolute;
		top: 17%;
		left: -30%;
		display: flex;
		flex-direction: column;
		align-items: center;

		h2 {
			font-size: 2vw;
			font-weight: 400;
			text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
			margin-bottom: 0;
		}

		span {
			margin: 0;
			font-size: 3vw;
			line-height: 1.2;
			font-weight: 700;
			color: $gold;
			text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
			margin-bottom: 0;
		}
	}

	.fuel {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 2%;
		border-bottom: 2px solid #000;
		position: relative;

		&::before {
			@extend %pseudo;

			left: -3.5%;
			bottom: 0;
			width: 1vw;
			height: 5vw;
			border-left: 2px solid #000;
			transform: rotate(-10deg);
		}

		.indicator {
			background: #000;
			width: 100%;
			height: 2vh;
			position: relative;
			align-self: flex-end;
			-webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 3% 100%);
			clip-path: polygon(0 0, 100% 0, 100% 100%, 3% 100%);
		}

		.fill {
			height: inherit;
			background: $gold;
		}

		svg {
			color: $gold;
			background: #000;
			width: 5vw;
			height: 5vh;
			padding: 3% 4%;
			padding-left: 6%;
			-webkit-clip-path: polygon(20% 0%, 80% 0%, 95% 100%, 21% 100%);
			clip-path: polygon(20% 0%, 80% 0%, 95% 100%, 21% 100%);
		}
	}

	.gear {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		&_number {
			font-size: 4vw;
			font-style: italic;
			color: #da2a2a;
			text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
			margin-right: 2%;
		}

		&-icon {
			width: 3vw;
			height: 3vw;
			background: url(../../../images/gear.svg) no-repeat;
			background-color: #000;
			border-radius: 50%;
		}
	}

	.rpm {
		display: flex;
		justify-content: space-between;
		border-top: 2px solid #000;
		padding-top: 2%;
		padding-left: 4%;
		position: relative;

		&::before {
			@extend %pseudo;

			left: -0.5vw;
			top: 0;
			width: 1vw;
			height: 5vw;
			border-left: 2px solid #000;
			transform: rotate(10deg);
		}

		.logo {
			margin-right: 4%;
			text-align: center;

			h2 {
				margin: 0;
				font-size: 1.3vw;
				line-height: 1.5;
				font-weight: 200;
				color: #00ffea;
				background: #000;
				width: 4vw;
				height: 2vw;
				padding: 3% 6%;
				margin-bottom: 2%;
				-webkit-clip-path: polygon(0 0, 100% 0%, 92% 100%, 0 100%);
				clip-path: polygon(0 0, 100% 0%, 92% 100%, 0 100%);
			}

			span {
				font-weight: 700;
				font-size: 1vw;
				color: #000;
				white-space: nowrap;
			}
		}

		.indicator {
			background: transparent;
			outline: 2px solid #000;
			margin-top: 1%;
			width: 100%;
			height: 2.5vh;
			position: relative;

			p {
				position: absolute;
				width: 100%;
				margin: 0;
				top: 0;
				left: 0;
				font-family: 'FuturicaBook';
				text-align: center;
				font-size: 1vw;
				line-height: 1.4;
				font-weight: 700;
				color: #000;
				white-space: nowrap;
			}
		}

		.fill {
			height: inherit;
			transition: all 0.5s ease;

			&.green {
				background: linear-gradient(to right, #37aa37, #0f9b0f);
			}

			&.yellow {
				background: linear-gradient(to right, rgb(246, 210, 47), rgb(230, 197, 10));
			}

			&.red {
				background: linear-gradient(to right, #cc413e, #ce201d);
			}
		}
	}

	.info {
		position: absolute;
		right: 0;
		bottom: 4%;
		width: 64%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-left: auto;

		&_turn-signals {
			svg.active {
				animation: flickerAnimation 1s infinite;
			}
		}

		svg {
			display: inline-flex;
			width: 2vw;
			height: 2vw;
			fill: #000;
			filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.5));

			&.active {
				fill: $gold;
			}
		}
	}
}

@media (max-width: 1024px) {
	.speedometer {
		.speed {
			h2 {
				text-shadow: -0.5px -0.5px 0 #000, 0.5px -0.5px 0 #000, -0.5px 0.5px 0 #000,
					0.5px 0.5px 0 #000;
			}

			span {
				text-shadow: -0.5px -0.5px 0 #000, 0.5px -0.5px 0 #000, -0.5px 0.5px 0 #000,
					0.5px 0.5px 0 #000;
			}
		}

		.fuel {
			border-bottom: 1px solid #000;

			&::before {
				left: -4%;
				height: 8vh;
				border-left: 1px solid #000;
			}

			.indicator {
				height: 1.5vh;
			}

			svg {
				width: 5vw;
				height: 3vh;
			}
		}

		.gear {
			&_number {
				text-shadow: -0.5px -0.5px 0 #000, 0.5px -0.5px 0 #000, -0.5px 0.5px 0 #000,
					0.5px 0.5px 0 #000;
			}
		}

		.rpm {
			border-top: 1px solid #000;

			&::before {
				left: -4%;
				height: 8vh;
				border-left: 1px solid #000;
			}

			.indicator {
				height: 2vh;
				outline: 1px solid #000;
			}
		}

		.info {
			bottom: 6%;
		}
	}
}
