.phone {
	position: absolute;
	min-height: auto !important;
	right: 0;
	bottom: 0;
	padding-top: 2.9vw;
	padding-right: 4.23vw;
	padding-left: 14.7vw;
	padding-bottom: 2.5vw;
	width: 35vw;
	height: 40vw;
	overflow: hidden;
	background: #000;
	background: url(../../../images/phone/iphone.svg) right center no-repeat;

	&_container {
		position: relative;
		min-height: 100%;
		height: 100%;
		overflow: hidden;
		border-radius: 1.5vw;
		background-image: url(../../../images/phone/iphone_bg.jpg);
		background-position: left center;
		background-repeat: no-repeat;
		background-size: cover;
		z-index: 1;
	}

	.brow {
		position: absolute;
		width: 25%;
		height: 4.5%;
		top: 7%;
		right: 22%;
		z-index: 10;
		background: #000;
		border-bottom-left-radius: 1.2vw;
		border-bottom-right-radius: 1.2vw;

		&::before {
			content: '';
			position: absolute;
			right: 24%;
			top: 18%;
			width: 0.8vw;
			height: 0.8vw;
			border-radius: 50%;
			background: #262626;
		}

		&::after {
			content: '';
			position: absolute;
			left: 32%;
			top: 35%;
			width: 30%;
			height: 0.2vw;
			border-radius: 10vw;
			background: #262626;
		}
	}

	&_status-bar {
		padding: 4% 6% 1%;
		display: flex;
		justify-content: space-between;
		font-size: 0.8vw;

		.icons {
			display: flex;
			align-items: center;

			svg {
				width: 1vw;
				height: 1vw;
				fill: currentColor;

				&:last-of-type {
					margin-left: 8%;
				}
			}
		}
	}

	&_content {
		height: 94%;
		padding: 8% 4% 0;

		.btn-close {
			position: absolute;
			bottom: 1%;
			left: 50%;
			transform: translateX(-50%);
			width: 50%;
			height: 1.5vh;
			color: currentColor;

			&:hover {
				opacity: 0.6;
			}

			&::before {
				@extend %pseudo;

				left: 0;
				width: 100%;
				height: 0.3vw;
				background: currentColor;
				border-radius: 1vw;
			}
		}
	}

	&_search {
		position: relative;
		display: block;
		margin-bottom: 4%;
		width: 100%;

		input {
			padding: 3% 4%;
			padding-left: 12%;
			width: 100%;
			font-size: 1vw;
			line-height: 1;
			border: none;
			border-radius: 0.5vw;
			background: #424347;
			color: #fff;
		}

		svg {
			position: absolute;
			width: 1.3vw;
			height: 1.3vw;
			bottom: 20%;
			left: 2%;
			fill: #757575;
		}
	}

	ul {
		@extend %list;

		&:hover {
			&::-webkit-scrollbar-thumb {
				border: none;
				background-clip: padding-box;
				background-color: #b8b8b8;
				border-radius: 6px;
			}
		}

		&::-webkit-scrollbar {
			width: 2px;
			opacity: 0;
		}

		&::-webkit-scrollbar-track {
			background-color: transparent;
		}

		&::-webkit-scrollbar-thumb {
			background-color: transparent;
		}

		&.apps {
			display: flex;
			flex-wrap: wrap;

			li {
				margin-bottom: 5%;
				width: 33%;

				&:hover img {
					filter: brightness(0.8);
				}
			}

			span {
				display: block;
				white-space: nowrap;
				text-align: center;
				font-size: 0.9vw;
				font-weight: 300;
				line-height: 2;
			}

			img {
				margin: 0 auto;
				display: block;
				width: 3.8vw;
				height: 3.8vw;
			}
		}

		&.dock {
			position: absolute;
			padding: 5%;
			left: 3%;
			bottom: 2%;
			width: 95%;
			height: 5.3vw;
			display: flex;
			justify-content: space-between;
			border-radius: 1.4vw;
			background: rgba(216, 77, 75, 0.7);

			li {
				width: 3.8vw;
				height: 3.8vw;

				&:hover img {
					filter: brightness(0.8);
				}

				img {
					display: block;
					width: inherit;
					height: inherit;
				}
			}
		}
	}
}

@media (max-width: 1024px) {
	.phone {
		padding-left: 14.5vw;
		padding-right: 4.18vw;
	}
}
