@import './help';
@import './chat';
@import './speedometer';
@import './online';
@import './location';
@import './ammo';

.hud {
	background: transparent;
	overflow: hidden;

	&_main {
		position: absolute;
		bottom: 2%;
		right: 1%;
		padding: 0.3% 0.5%;
		display: flex;
		width: 18%;
		justify-content: space-between;
		align-items: center;
		background: linear-gradient(
			180deg,
			rgba(15, 15, 15, 0.5) 20%,
			rgba(39, 39, 39, 0.5) 75%
		);
		border-top: 2px solid $gold;

		svg {
			display: block;
			width: 1.5vw;
			height: 1.5vw;
			fill: $gold;
		}
	}

	&_money {
		width: 50%;
		border-right: 1px solid $gold;

		&-item {
			display: flex;
			align-items: center;

			span {
				padding-left: 0.5vw;
				font-size: 0.8vw;
				font-weight: 300;
				white-space: nowrap;
				text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
			}
		}
	}

	&_date {
		text-align: center;

		&-item {
			display: block;
			font-size: 0.8vw;
			font-weight: 300;
			line-height: 1.2;
			color: $gold;
			text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);

			&--time {
				font-size: 1.3vw;
				font-weight: 700;
				line-height: 1.1;
				color: #fff;
			}
		}
	}

	&_mic {
		svg {
			width: 2.5vw;
			height: 2.5vw;
			margin: 0 auto;
			transition: fill 0.2s ease;
		}

		&.speaker svg {
			fill: $pink;
		}
	}

	&_hunger {
		display: flex;
		align-items: flex-end;
		position: absolute;
		left: 0;
		top: -52%;

		&-icon {
			width: 3vw;
			height: 3vw;
			color: $gold;
		}

		&-text {
			padding-left: 6%;
			font-size: 1vw;
			font-weight: 300;
			text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
		}
	}

	&_donation {
		position: absolute;
		top: 20%;
		left: -20%;
		padding: 2% 4%;
		background: $gold;
		text-align: center;
		font-size: 0.7vw;
		line-height: 0.9;
		font-weight: 700;
		color: #343434;
	}
}
