#weather {
	background-image: url(../../../images/phone/weather_bg.jpg);
	background-size: cover;
	background-position: center;
	text-align: left;

	.weather {
		&_location {
			margin-bottom: 10%;

			h2 {
				margin: 0 0 2%;
				font-size: 2.2vw;
				font-weight: 400;
				text-align: center;
			}

			.temperature {
				display: block;
				font-size: 4.5vw;
				text-align: center;
			}
		}

		&_current {
			padding: 4% 3%;
			border-top: 1px solid #85858b;
			border-bottom: 1px solid #85858b;

			ul {
				display: flex;
				justify-content: space-between;
				text-align: center;
			}

			li {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: space-between;
				font-size: 1.2vw;

				&:first-of-type {
					font-weight: 700;
				}

				span {
					&:not(:last-of-type) {
						margin-bottom: 12%;
					}
				}
			}
		}

		&_week {
			padding: 6% 2% 3%;
			border-bottom: 1px solid #85858b;

			li {
				position: relative;
				margin-bottom: 6%;
				display: flex;
				justify-content: space-between;
				font-size: 1.2vw;

				.icon {
					position: absolute;
					margin: 0 auto;
					left: 60%;
				}
			}
		}
	}
}
