.darknet {
	display: flex;
	background: url(../../../../images/darknet_bg.jpg) center bottom no-repeat;
	background-size: cover;

	&_wrapper {
		position: relative;
		width: 85%;
		align-self: center;
		display: flex;
		padding-top: 3.75%;
		padding-right: 2%;
		justify-content: space-between;
		margin: 0 auto;
		background: url(../../../../images/darknet-browser_bg.png) no-repeat;
		background-size: cover;
	}
}

.security-laptop {
	padding: 2% 0;
	width: 100%;

	&_title {
		width: 60%;
		border-color: $pink;
	}

	form {
		padding-top: 3%;
		width: 50%;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
	}

	.fields {
		display: flex;
		justify-content: space-between;
		margin-bottom: 4%;
		padding-bottom: 4%;
		border-bottom: 2px solid rgb(155, 15, 61);

		input {
			width: 48%;
			background: none;
			outline: none;
			text-align: center;
			font-family: inherit;
			font-size: 1.5vw;
			line-height: 1.5;
			color: #fff;
			border: 1px solid rgb(155, 15, 61);

			&::placeholder {
				color: #fff;
			}
		}
	}

	ul {
		width: 100%;
		margin-bottom: 8%;

		li {
			padding: 1% 2%;
			width: 100%;
			border: 1px solid rgb(155, 15, 61);
			text-align: center;
			font-size: 1.2vw;
			font-weight: 700;

			&.active {
				background: rgb(155, 15, 61);
			}
		}
	}

	&_price {
		padding-left: 0;
		font-weight: 700;
		font-size: 2.5vw;
		text-align: center;
		border-color: $pink;
	}

	.buttons {
		padding-top: 5%;
		display: flex;
		justify-content: space-between;

		button {
			width: 35%;
		}
	}
}
