@import './login.scss';
@import './register.scss';

.auth {
	background: url(../../../images/auth_bg.jpg) center no-repeat;
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;

	&_container {
		position: relative;
		padding: 10% 2% 1.8% 18%;
		width: 56%;
		display: flex;
		flex-direction: column;
		background-image: url(../../../images/login_bg.png);
		background-size: cover;
		background-repeat: no-repeat;

		.logo {
			width: 67%;
			height: 10vh;
			margin-bottom: 8%;
			z-index: 2;
			fill: #fff;
		}
	}

	&_form {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		z-index: 2;

		h2 {
			margin: 10% 0;
			margin-bottom: 5%;
			font-size: 1.3vw;
			font-weight: 300;
			text-align: left;
		}

		button {
			@extend %button;

			margin-right: auto;
			margin-left: -5%;
			padding: 2% 8%;
			color: #fff;
			font-family: 'HelveticaNeue';
			font-weight: 200;
			font-size: 2vw;
			border: 1px solid #fff;
			margin-top: auto;
			cursor: pointer;
			transition: opacity 0.3s ease;

			&:hover {
				opacity: 0.5;
			}
		}

		input:hover {
			background: rgba(255, 255, 255, 0.8);
		}

		label {
			position: relative;
			margin-bottom: 6%;
			width: 70%;
			display: block;
		}
	}

	&_input {
		width: 100%;
		border: none;
		padding: 1% 2%;
		font-family: 'FuturicaBook';
		font-size: 1.8vw;
		outline: none;
		transition: background 0.3s ease;

		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	&_error {
		position: absolute;
		margin: 0;
		font-size: 1.1vw;
		font-weight: 300;
		color: #d10606;
	}

	&_link {
		position: absolute;
		bottom: 3%;
		font-family: 'HelveticaNeue';
		font-weight: 300;
		font-size: 1.3vw;
		color: #facc21;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}
}

@media (max-width: 1024px) {
	.auth {
		&_container {
			width: 75%;
			background-position: 30% center;
		}

		&_form {
			button {
				font-size: 2.2vw;
			}
		}
	}
}
