.spawn {
	display: flex;
	justify-content: space-between;
	overflow: hidden;

	&_item {
		position: relative;
		padding-top: 25vh;
		min-height: 100%;
		width: 33.33%;
		background-size: cover;
		background-repeat: no-repeat;
		transition: background 0.5s ease;

		&#exit {
			background-image: url(../../../images/spawn/exit-blurred.jpg);

			&::before {
				background-image: url(../../../images/girl1.png);
			}
		}

		&#faction {
			background-image: url(../../../images/spawn/faction-blurred.jpg);

			&::before {
				background-image: url(../../../images/girl2.png);
			}
		}

		&#house {
			background-image: url(../../../images//spawn/house-blurred.jpg);

			&::before {
				background-image: url(../../../images/girl3.png);
			}
		}

		&::before {
			@extend %pseudo;

			top: 60%;
			bottom: 0;
			left: 20%;
			right: 5%;
			z-index: 1;
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center bottom;
		}

		&::after {
			@extend %pseudo;

			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			border-right: 1px solid gold;
			border-left: 1px solid gold;
			transition: border-color 0.5s ease;
		}

		&-container {
			min-height: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			transition: all 0.5s ease;

			&::after {
				@extend %pseudo;

				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				background: rgba(50, 12, 70, 0.75);
				z-index: 0;
				transition: background 0.5s ease;
			}

			* {
				z-index: 3;
			}
		}

		.logo {
			position: absolute;
			top: 4%;
			text-align: center;
		}

		.title {
			margin-bottom: 10.5%;
			font-size: 1.5vw;
			font-weight: 700;
		}

		.desc {
			margin: 0;
			font-size: 1.3vw;
			font-weight: 200;
		}

		.icon {
			width: 14vh;
			height: 14vh;
			fill: #fff;
			transition: fill 0.5s ease;
		}

		button {
			@extend %button;

			padding: 1% 2%;
			border: 1px solid #fff;
			font-size: 1.5vw;
			font-weight: 200;
			color: #fff;
			transition: all 0.3s ease;

			&:hover {
				background: #fff;
				color: #000;
			}
		}

		.timer {
			margin: 0;
			font-size: 1.5vw;
			font-weight: 200;
			color: #f6f5f9;
		}

		.timer,
		button {
			display: none;
		}

		&:not(.is-disabled):hover {
			&#exit {
				background-image: url(../../../images/spawn/exit.jpg);
			}

			&#faction {
				background-image: url(../../../images/spawn/faction.jpg);
			}

			&#house {
				background-image: url(../../../images/spawn/house.jpg);
			}

			&::after {
				border-color: transparent;
			}

			> div::after {
				background: rgba(0, 0, 0, 0.35);
			}

			.logo {
				display: none;
			}

			.icon {
				fill: $gold;
			}

			button {
				display: block;
			}
		}

		&.is-disabled {
			&::before {
				filter: grayscale(100%);
			}

			&::after {
				border-color: transparent;
			}

			> div::after {
				background: rgba(54, 54, 54, 0.75);
			}

			.logo,
			.desc,
			button {
				display: none;
			}

			.title {
				margin-bottom: 3%;
			}

			.icon {
				fill: #93291e;
			}

			.timer {
				display: block;
			}
		}
	}
}
