.pier {
	@extend %window;

	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/pier_bg.jpg);
	padding: 2% 4%;
	color: #fff;

	&.airport {
		background-image: url(../../../images/airport_bg.jpg);
	}

	&_vehicles {
		height: 35vh;
		overflow-y: scroll;
		padding-right: 5%;
		margin-bottom: 20px;

		&-item {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			border: 1px solid #fff;
			padding: 10px;
			margin-bottom: 10px;

			p {
				margin: 0;
				padding: 2px 5px;
				font-size: 1.3vw;
			}

			button {
				@extend %button;

				padding: 1%;
				font-size: 1.3vw;
				font-weight: 400;
				color: #fff;
				width: 20%;
				border: 1px solid #fff;
				transition: all 0.3s ease;

				&:disabled {
					color: $purple;
					background: $gold;
				}
			}
		}
	}

	&_content {
		width: 90%;
		margin: 0 auto;
		padding: 0 4%;
		padding-top: 2%;
		margin-top: 2%;
		margin-bottom: 5%;

		h2 {
			text-align: left;
			font-size: 1.5vw;
			margin: 0;
			margin-bottom: 4%;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}
}

@media (max-width: 1024px) {
	.ems-garage {
		width: 80%;
	}
}
