.resources-processing {
	@extend %window;

	position: relative;
	background-image: url(../../../images/goldMiner-bg.jpg);
	width: 50%;
	padding: 1% 3%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_current {
		@extend %button;

		margin-left: auto;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		white-space: nowrap;
		font-size: 1.2vw;
		font-weight: 300;
		color: #fff;

		svg {
			margin-left: 1%;
			width: 2vw;
			height: 2vw;
			fill: $gold;
		}
	}

	&_items {
		padding-top: 3%;
		display: flex;

		ul {
			position: absolute;
			top: 35%;
			right: 5%;
			padding: 2%;
			width: 40%;
			background-image: url(../../../images/legal-bg.jpg);
			text-align: center;
			box-shadow: 0px 4px 4px 4px rgba(0, 0, 0, 0.1);
			z-index: 3;
		}

		li {
			padding: 1% 0;
			font-weight: 300;

			&:not(:last-of-type) {
				border-bottom: 1px solid $gold;
			}
		}
	}

	&_container {
		padding-top: 3%;
		margin-bottom: 10%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&_info {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 20%;
		font-size: 1vw;
		font-weight: 300;
		color: $gold;

		div {
			display: flex;
			align-items: center;
			width: 40%;
		}

		span {
			margin: 0 auto;
			text-align: center;
			color: #fff;
		}

		img {
			padding: 6%;
			width: 70%;
			display: block;
			height: auto;
			filter: invert(1);
		}
	}

	&_count {
		width: 30%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		h3 {
			margin: 0;
			margin-right: 3%;
			font-size: 1.3vw;
			font-weight: 300;

			b {
				color: $gold;
			}
		}
	}

	&_footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&--illegal {
		background-image: url(../../../images/illegal-bg.jpg);
		border-color: $pink;

		.resources-processing {
			&_title {
				border-color: $pink;
			}

			&_current {
				svg {
					fill: $pink;
				}
			}

			&_count {
				b {
					color: $pink;
				}

				svg {
					fill: #fff;
				}
			}

			&_items {
				ul {
					background-image: url(../../../images/illegal-bg.jpg);

					li {
						border-color: $pink;
					}
				}
			}

			&_info {
				color: $pink;
			}
		}
	}
}
