.gang-docs {
	background: url(../../../images/gang-docs_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	min-height: auto !important;
	position: absolute;
	width: 48%;
	padding: 1% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_gang {
		font-size: 2.2vw;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
	}

	&_container {
		margin-bottom: 2%;
		padding: 4% 0;
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		p {
			font-weight: 300;
			color: #4d4d4d;

			b {
				color: #000;
			}
		}
	}

	&_footer {
		display: flex;
		flex-direction: column;

		button {
			margin-left: auto;
			border-color: #000;
			color: #000;
		}
	}

	&_remark {
		font-size: 1vw;
		font-weight: 300;
		color: #000;
	}
}
