.gang-leader {
	@extend %window;

	bottom: 2vw;
	left: 10%;
	width: 75%;
	height: 45vw;
	padding: 1.5% 6.8%;
	background: url(../../../images/ipad.svg) center no-repeat;
	border: none;
	box-shadow: none;

	&_container {
		padding: 1% 0;
		height: 100%;
		overflow: hidden;
		position: relative;
		background-color: #000;
		border-radius: 1.5vw;
	}

	&_content {
		min-height: 100%;
		display: flex;
	}

	&_status-bar {
		position: absolute;
		padding: 0 0.5%;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.time {
			font-size: 1vw;
			font-weight: 300;
		}

		.icons {
			width: 30%;
			display: flex;
			justify-content: flex-end;

			svg {
				margin-left: 1%;
			}
		}
	}

	&_sidebar {
		padding-top: 10%;
		width: 45%;
		border-right: 1px solid #2b2c2e;

		h1 {
			padding: 1% 4%;
			margin: 0;
			font-size: 1.8vw;
		}

		.profile {
			margin-bottom: 8%;
			padding: 2% 4%;
			display: flex;
			align-items: center;
			background-color: #1c1c1e;
			border-top: 1px solid #2b2c2e;
			border-bottom: 1px solid #2b2c2e;

			img {
				height: 5vw;
			}

			&_text {
				margin-left: 4%;

				p {
					margin: 0;

					&.name {
						font-size: 1.3vw;
					}

					&.rank {
						font-size: 1vw;
						font-weight: 300;
					}
				}
			}
		}

		.tabs {
			background-color: #1c1c1e;
			border-top: 1px solid #2b2c2e;
			border-bottom: 1px solid #2b2c2e;

			&_list {
				&-item {
					position: relative;
					padding: 1.5% 4%;
					padding-right: 0;
					display: flex;
					align-items: center;

					&:hover {
						background-color: #2b2c2e;
					}

					&:not(:last-of-type) {
						&::after {
							@extend %pseudo;

							right: 0;
							bottom: 0;
							width: 84%;
							height: 1px;
							background-color: #2b2c2e;
						}
					}

					&.active {
						background-color: #0f65bc;

						&::after {
							background-color: transparent;
						}
					}
				}
			}

			&_icon {
				padding: 0.3vw;
				display: flex;
				align-items: center;
				border-radius: 0.45vw;

				svg {
					width: 1.5vw;
					height: 1.5vw;
				}
			}

			&_title {
				margin: 0;
				margin-left: 4%;
				width: 100%;
				font-size: 1.1vw;
				font-weight: 300;
			}
		}
	}

	&_tab {
		padding: 0 1.5%;
		width: 100%;
	}

	&_navigation {
		position: relative;
		padding: 5% 0%;
		width: 100%;
		height: 12vh;
		display: flex;
		align-items: center;
		justify-content: space-between;

		button {
			@extend %button;

			display: flex;
			align-items: center;
			color: #0a84ff;
			transition: opacity 0.2s ease;

			&:hover {
				opacity: 0.5;
			}

			span {
				font-size: 1.1vw;
				font-weight: 300;
				line-height: 1;
			}

			svg {
				width: 2vw;
				height: 2vw;
			}
		}

		&-title {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			margin: 0 auto;
			font-size: 1.2vw;
			font-weight: 400;
		}

		&-action {
			margin-left: auto;
		}
	}

	&_button {
		margin-top: 1%;
		padding: 0 2%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: #1c1c1e;
		border-radius: 0.8vw;
		font-size: 1.1vw;
		line-height: 2.3;
		font-weight: 300;

		&:hover {
			background-color: #2b2c2e;
		}

		svg {
			fill: #9898a0;
		}

		&--primary {
			.name {
				color: #0a84ff;
			}
		}

		&--danger {
			.name {
				color: #ff0a0a;
			}
		}

		&--disabled:hover {
			background-color: #1c1c1e;
		}

		.selected {
			margin-right: 1%;
			margin-left: auto;
			color: #9898a0;
		}

		.active svg {
			fill: #0a84ff;
		}
	}

	&_input {
		margin-bottom: 2.5%;
		position: relative;

		input {
			padding: 0 2%;
			width: 100%;
			display: block;
			border: none;
			outline: none;
			background-color: #1c1c1e;
			border-radius: 0.8vw;
			font-family: inherit;
			font-size: 1.1vw;
			font-weight: 300;
			line-height: 2.5;
			color: #fff;
		}

		&-reset {
			position: absolute;
			top: 50%;
			right: 2%;
			transform: translateY(-50%);
			width: 1.5vw;
			height: 1.5vw;
			fill: #9898a0;
		}
	}

	&_list {
		max-height: 35vw;
		overflow-y: scroll;
		border-radius: 0.8vw;
		background-color: #1c1c1e;

		&:hover {
			&::-webkit-scrollbar-thumb {
				border: none;
				background-clip: padding-box;
				background-color: #b8b8b8;
				border-radius: 6px;
			}
		}

		&::-webkit-scrollbar {
			width: 2px;
			opacity: 0;
		}

		&::-webkit-scrollbar-track {
			background-color: transparent;
		}

		&::-webkit-scrollbar-thumb {
			background-color: transparent;
		}

		&-item {
			position: relative;
			margin: 0;
			border-radius: 0;

			&:not(:last-of-type) {
				&::after {
					@extend %pseudo;

					right: 0;
					bottom: 0;
					width: 98%;
					height: 1px;
					background-color: #2b2c2e;
				}
			}

			&.inactive {
				opacity: 0.5;
			}
		}
	}

	&_group {
		&-title {
			margin: 0;
			padding: 3% 2% 0.5%;
			text-transform: uppercase;
			font-size: 1vw;
			font-weight: 300;
			color: #908f95;
		}
	}

	&_modal {
		display: flex;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0, 0, 0, 0.5);

		&-container {
			padding: 0% 0%;
			background: #d6d6d7;
			background-size: cover;
			width: 40%;
			margin: auto;
			border-radius: 1vw;
			overflow: hidden;
		}

		&-title {
			margin-bottom: 0;
			font-size: 1.4vw;
			font-weight: 400;
			text-align: center;
			color: #000;
		}

		&-descr {
			margin: 0;
			padding: 0 4%;
			text-align: center;
			font-size: 1.1vw;
			font-weight: 300;
			color: #000;
		}

		&-buttons {
			margin-top: 5%;
			border-top: 1px solid #c7cccf;

			button {
				padding: 4%;
				width: 50%;
				background: none;
				border: none;
				outline: none;
				text-align: center;
				font-family: inherit;
				font-size: 1.4vw;
				font-weight: 300;
				color: #0a84ff;

				&:hover {
					background: #b4b9bc;
				}
			}
		}

		&-confirm {
			font-weight: 500 !important;
			border-left: 1px solid #c7cccf !important;
		}
	}
}
