.btn {
	@extend %button;
}

.btn-outlined {
	@extend %button;

	min-width: 9vw;
	padding: 1vw 2vw;
	font-weight: 200;
	font-size: 1.8vw;
	color: #fff;
	text-align: center;
	transition: opacity 0.3s ease;
	border: 1px solid #fff;

	&:hover {
		opacity: 0.6;
	}

	&:active {
		opacity: 0.3;
	}

	&:disabled {
		opacity: 0.6;
	}
}
