.bag-shop {
	@extend %window;

	top: 50%;
	left: 50%;
	padding: 2%;
	width: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/bagshop-bg.jpg);
	color: #fff;

	&_remark {
		width: 30%;
		font-size: 1.2vw;
		text-align: left;
		border-bottom: 1px solid $gold;

		strong {
			font-weight: 400;
			color: $gold;
		}
	}

	&_content {
		padding-top: 5%;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
	}

	&_footer {
		padding-top: 8%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;

		button {
			padding: 1% 4%;
		}

		.exit-btn {
			margin-right: auto;
		}
	}

	&_price {
		width: 40%;
		margin-right: 3%;
		border-bottom: 1px solid $gold;
		font-size: 2vw;
		text-align: right;
	}

	&_items {
		width: 65%;

		&_list {
			height: 20vh;
			overflow-y: scroll;
			padding-right: 20px;
		}

		&-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #fff;

			&.is-active {
				border-color: $gold;
			}

			img {
				width: 5vw;
				height: 5vw;
				filter: invert(1);
			}

			.slots {
				font-size: 1.2vw;
				line-height: 1.2;
				color: $gold;
			}
		}
	}
}
