.car-market {
	@extend %window;

	left: 50%;
	top: 50%;
	padding: 1% 3% 2%;
	width: 55%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/car-market_bg.jpg);

	&_title {
		width: 80%;
	}

	&_info {
		margin-right: auto;
		margin-bottom: 4%;
		padding-top: 3%;
		width: 78%;
		display: flex;
		flex-direction: column;

		&-wrapper {
			display: flex;
			align-items: center;
		}

		&-item {
			margin-bottom: 1%;
			display: flex;
			align-items: center;
			font-size: 1vw;
			font-weight: 400;

			h3 {
				margin: 0;
				margin-right: 1%;
				font-weight: 400;
			}

			span {
				color: $gold;
			}
		}
	}

	&_form {
		display: flex;
		align-items: center;
		justify-content: space-between;

		label {
			width: 70%;

			h4 {
				margin: 0;
				font-size: 1.2vw;
				font-weight: 300;
				text-align: right;
			}

			input {
				padding: 1% 2%;
				width: 100%;
				text-align: right;
				background: transparent;
				border: none;
				outline: none;
				border-bottom: 1px solid #fff;
				font-size: 3vw;
				font-weight: 700;
				color: $gold;
			}
		}
	}

	.buttons {
		width: 20%;

		button {
			margin-bottom: 4%;
			width: 100%;
		}
	}

	&_tuning {
		width: 50%;

		ul {
			padding: 0 2%;
			height: 20vh;
			overflow-y: scroll;
		}

		li {
			padding: 2% 0;
			display: flex;
			align-items: center;
			border-bottom: 1px solid #fff;
			font-size: 1vw;
		}

		span {
			margin-left: 4%;
			text-align: center;
			font-weight: 300;
			color: $gold;
		}

		svg {
			width: 2vw;
			height: 2vw;
			display: block;
			fill: #fff;
		}
	}

	&_footer {
		padding-top: 5%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		h4 {
			margin: 0;
			font-weight: 300;
			text-align: right;
		}

		.price {
			font-size: 2.5vw;
			font-weight: 700;
			color: $gold;
			border-bottom: 1px solid #fff;
		}
	}

	&_price {
		width: 50%;
	}
}
