#fleeca {
	background-image: url(../../../images/bank-bg.jpg);
	background-size: cover;
	background-position: center;

	.phone_content {
		padding-left: 0;
		padding-right: 0;
	}

	.fleeca {
		&_header {
			position: relative;
			padding: 0 4%;
			z-index: 2;

			.logo {
				margin-bottom: 4%;
				width: 45%;

				img {
					display: block;
					width: auto;
				}

				p {
					margin: 0;
					text-align: center;
					font-size: 0.7vw;
					font-weight: 500;
					opacity: 0.95;
				}
			}
		}

		&_form {
			position: relative;
			margin-top: -5%;
			padding: 8% 4% 4%;
			background: #fff;
			text-align: center;
			color: $purple;

			&-field {
				padding: 0.8vw 2vw;
				display: flex;
				flex-direction: column;
				align-items: center;
				width: 100%;
				border-radius: 1vw;
				background-color: #fff;
				box-shadow: 0px 20px 25px -15px rgba($color: $purple, $alpha: 0.7);
				font-size: 1vw;
				line-height: 1.5;
				font-weight: 700;

				&:not(:last-of-type) {
					margin-bottom: 5%;
				}

				.info {
					font-size: 0.8vw;
					font-weight: 500;
					color: #a2a2a2;
				}

				input {
					width: 100%;
					border: none;
					border-bottom: 1px solid $purple;
					text-align: center;
					outline: none;
					font: inherit;
					color: $purple;

					&::placeholder {
						color: $purple;
					}
				}
			}

			&-submit {
				@extend %button;

				// bottom: -30%
				margin-top: -30%;
				padding: 4% 8%;
				// width: 100%;
				background-color: $purple;
				border-radius: 0.6vw;
				font-size: 1.2vw;
				font-weight: 500;
				color: $gold;
				transition: box-shadow 0.3s ease;

				&:hover {
					box-shadow: 5px 20px 25px -15px rgba($color: $purple, $alpha: 1);
				}
			}

			&-title {
				margin-bottom: 2%;
				font-size: 1.2vw;
			}
		}

		.bank-card {
			position: static;
			padding: 4% 6%;
			width: 100%;
			height: 8vw;

			.name {
				font-size: 1vw;
			}

			.number {
				font-size: 1.1vw;
			}
		}
	}
}
