.contacts_editor {
	.navigation {
		padding: 0 3%;
		display: flex;
		justify-content: space-between;

		button {
			font-size: 0.9vw;
			color: $blue;
		}
	}

	form {
		padding-top: 8%;

		.main-fields {
			padding-left: 2%;
			margin-bottom: 15%;
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
		}

		.avatar {
			padding-bottom: 4%;
			position: relative;

			svg {
				width: 3vw;
				height: 3vw;
				fill: #757575;
			}

			span {
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				text-align: center;
				font-size: 0.8vw;
				color: $blue;
			}
		}

		.inputs {
			margin-left: 4%;

			input {
				padding: 3% 0;
				padding-left: 5%;
				width: 100%;
				border: none;
				outline: none;
				border-bottom: 1px solid #eaeaea;
				font-size: 1vw;
			}
		}

		.phone-fields {
			padding-left: 5%;

			&_item {
				padding: 3% 0;
				display: flex;
				align-items: center;
				border-bottom: 1px solid #eaeaea;

				i {
					margin-right: 3%;
					width: 1.5vw;
					height: 1.5vw;
					background-color: #56d171;
					border-radius: 50%;

					svg {
						position: absolute;
						width: inherit;
						height: inherit;
						fill: #fff;
					}
				}

				input {
					outline: none;
					border: none;
					font-size: 1vw;
				}
			}
		}

		.btn-remove {
			position: absolute;
			left: 5%;
			bottom: 6%;
			font-size: 1vw;
			color: red;
		}
	}
}
