.cityhall-bank {
	@extend %window;

	background-image: url(../../../images/cityhall/cityhall-bg.jpg);
	width: 55%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_price {
		padding-top: 3%;
		display: flex;
		margin: 0 auto;
		width: 85%;
		padding-bottom: 5px;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #fff;

		h3 {
			text-align: left;
			font-weight: 300;
			margin: 0;
			margin-bottom: 0;
			padding-left: 20px;
			font-size: 2vw;
		}

		span {
			width: auto;
			padding-left: 20px;
			font-size: 4vw;
			font-weight: 700;
			color: $gold;
			border: none;
		}
	}

	.remark {
		text-align: center;
		color: $gold;
		font-size: 1.2vw;
		margin-top: 5px;
		margin-bottom: 50px;
	}

	&_footer {
		display: flex;
		justify-content: flex-end;
		width: 100%;

		button {
			width: 25%;
			margin-right: 5px;
		}
	}
}
