#maps {
	background: #1f1e23;
	text-align: left;

	.maps_items {
		height: 28vw;
		overflow-y: scroll;

		li {
			padding: 3%;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 1.1vw;
			font-weight: 300;
			transition: opacity 0.3s ease;

			&:not(:last-of-type) {
				border-bottom: 1px solid #272729;
			}

			&:hover {
				opacity: 0.6;
			}

			svg {
				fill: #4b4a4f;
			}

			.name {
				max-width: 90%;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}
		}
	}
}
