.contacts_contact {
	.navigation {
		padding: 0 3%;
		padding-left: 0;
		display: flex;
		justify-content: space-between;

		button {
			font-size: 0.9vw;
			line-height: 1;
			color: $blue;
		}

		.btn-back {
			display: flex;
			align-items: center;

			svg {
				width: 1.5vw;
				height: 1.5vw;
			}
		}
	}

	.header {
		margin-bottom: 4%;
		padding-bottom: 4%;
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid #eaeaea;

		.avatar {
			width: 4vw;
			height: 4vw;

			svg {
				width: 4vw;
				height: 4vw;
				fill: #757575;
			}
		}

		.name {
			margin: 0;
			font-size: 1.2vw;
			font-weight: 700;
		}

		.controls {
			padding-top: 5%;
			width: 90%;
			display: flex;
			justify-content: space-between;

			&_item {
				@extend %button;

				width: 4vw;
				text-align: center;
				text-transform: lowercase;
				font-size: 0.8vw;
				font-weight: 300;
				color: $blue;

				.icon {
					margin: 0 auto 10%;
					width: 2.3vw;
					height: 2.3vw;
					display: flex;
					justify-content: center;
					align-items: center;
					background: #0384f9;
					border-radius: 50%;

					svg {
						padding: 2%;
						width: 1.5vw;
						height: 1.5vw;
						fill: #fff;
					}
				}
			}
		}
	}

	.info {
		padding-left: 5%;

		&_item {
			border-bottom: 1px solid #eaeaea;

			h4 {
				margin: 0;
				font-size: 1vw;
				font-weight: 400;
			}

			p {
				margin: 0;
				font-size: 1vw;
				color: $blue;
			}
		}
	}

	.block-btn {
		@extend %button;

		position: absolute;
		bottom: 6%;
		font-size: 1vw;
		color: red;
	}
}
