.ems-garage {
	@extend %window;

	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../../images/ems_bg.jpg);
	padding: 2% 4%;
	color: #fff;
	border-color: #fff;

	&_header {
		text-align: left;
		padding-left: 10%;

		h1 {
			margin: 0;
			margin-bottom: 3%;
			font-size: 2vw;
		}

		.user-info {
			margin-bottom: 7%;

			p {
				font-weight: 700;
				margin: 0;
				font-weight: 300;
				font-size: 1.3vw;

				span {
					margin-left: 1%;
					font-weight: 700;
					color: #fff;
				}
			}
		}
	}

	&_vehicles {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 20px;

		&-item {
			display: flex;
			justify-content: space-between;
			align-items: center;
			background: #fff;
			width: 100%;
			border-left: 6px solid #ed1c24;
			padding: 10px;
			margin-bottom: 10px;

			&:nth-child(even) {
				background: #fac8c8;
				border-left: 6px solid #f54249;
			}

			p {
				margin: 0;
				padding: 2px 5px;
				font-size: 1.3vw;
				font-weight: 700;
				color: #000;
			}

			button {
				@extend %button;

				padding: 1%;
				font-size: 1.3vw;
				font-weight: 400;
				background: #000;
				color: #fff;
				width: 25%;
				border: 1px solid #000;
				transition: all 0.3s ease;

				&:disabled {
					color: #fff;
					background: #ed1c24;
				}
			}
		}
	}

	&_content {
		width: 80%;
		height: 35vh;
		background-color: rgba($color: #000000, $alpha: 0.25);
		margin: 0 auto;
		overflow-y: scroll;
		padding: 0 4%;
		padding-top: 2%;
		margin-bottom: 5%;

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.1);
		}

		&::-webkit-scrollbar-thumb {
			background: red;
		}

		h2 {
			text-align: left;
			font-size: 1.7vw;
			margin: 0;
			margin-bottom: 4%;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}
}

@media (max-width: 1024px) {
	.ems-garage {
		width: 80%;
	}
}
