.lsc {
	background: url(../../../images/lsc_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	width: 28%;
	min-height: 100%;
	height: 100%;
	margin-left: auto;

	&_container {
		position: relative;
		display: flex;
		min-height: 100%;
		height: 100%;
	}

	&_title {
		width: 95%;
	}

	&_characters {
		position: absolute;
		left: 1%;
		bottom: 2%;
		width: 15%;

		h4 {
			font-size: 1vw;
			font-weight: 200;
			margin: 0;
			text-align: left;
			line-height: 1.7;
		}

		progress {
			display: block;
			width: 100%;
			height: 1vh;
			color: $gold;
			background-color: #fff;
		}

		progress::-webkit-progress-bar {
			background-color: #fff;
		}
		progress::-webkit-progress-value {
			background-color: $gold;
		}
	}

	&_categories {
		background: url(../../../images/lsc_bg.jpg) no-repeat;
		background-size: cover;
		background-position: right;
		padding: 1% 2px;
		width: 14%;
		overflow-y: scroll;
		height: 100%;

		&::-webkit-scrollbar {
			width: 0.2vw;
		}

		h2 {
			position: absolute;
			top: 3.5vw;
			right: 8%;
			font-size: 1.3vw;
			font-weight: 300;
		}

		&-list {
			padding: 0 2%;
			padding-right: 8%;
		}

		&-item {
			width: 100%;
			padding: 2px;
			margin-bottom: 15%;

			&.active {
				background: $gold;

				.icon {
					fill: $purple;
				}
			}
		}

		.icon {
			display: block;
			width: 2.2vw;
			height: 2.2vw;
			margin: 0 auto;
			fill: #fff;
		}
	}

	&_category {
		padding-top: 2%;
		margin-bottom: auto;
		display: flex;
		align-items: center;

		h2 {
			margin: 0;
			margin-left: auto;
			font-size: 1.5vw;
			font-weight: 400;
		}

		.btn-close {
			display: flex;
			align-items: center;
			color: #fff;

			&:hover span {
				border-bottom: 1px solid transparent;
			}

			svg {
				width: 1.5vw;
				height: 1.5vw;
				fill: $gold;
			}

			span {
				margin-left: 1%;
				border-bottom: 1px solid #fff;
				font-size: 1.2vw;
			}
		}
	}

	&_content {
		display: flex;
		flex-direction: column;
		min-height: 100%;
		width: 100%;
		padding: 4% 6%;
		position: relative;
	}

	&_list {
		padding-right: 2%;
	}

	&_tab {
		overflow-y: scroll;
		height: 65vh;
		margin: auto 0;
	}

	&_item {
		border: 1px solid #fff;
		padding: 1.5% 3%;
		margin-bottom: 2%;
		font-weight: 300;
		text-align: left;
		font-size: 1.1vw;
		transition: all 0.5s ease;

		&:hover {
			opacity: 0.7;
		}

		&.disabled {
			pointer-events: none;
			opacity: 0.5;
		}

		&.active {
			background: #fff;
			color: $purple;
			font-weight: 700;

			&:hover {
				opacity: 1;
			}
		}
	}

	&_footer {
		.buttons {
			padding-top: 2%;
			display: flex;
			justify-content: space-between;

			button {
				width: 49%;
			}
		}
	}

	&_numbers {
		&-input {
			margin: 10% auto 0;
			width: 90%;
			display: block;
			padding: 1% 2%;
			border: 1px solid #fff;
			text-align: center;
			font-size: 1.2vw;
			line-height: 1.5;
			font-weight: 300;
			font-family: inherit;
			outline: none;
		}
	}
}

@media (max-width: 1024px) {
	.lsc {
		width: 32%;
	}
}
