.mafia-garage {
	@extend %window;

	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../../images/italian-mafia_bg.jpg);
	padding: 2% 4%;
	color: #fff;

	&_vehicles {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 20px;

		&-item {
			display: flex;
			justify-content: space-between;
			align-items: center;
			background: #fff;
			width: 100%;
			border-left: 6px solid $gold;
			padding: 10px;
			margin-bottom: 10px;

			&:nth-child(even) {
				background: #d3e2ff;
				border-left: 6px solid #153d89;
			}

			p {
				margin: 0;
				padding: 2px 5px;
				font-size: 1.3vw;
				font-weight: 700;
				color: #091326;
			}

			button {
				@extend %button;

				padding: 1%;
				font-size: 1.3vw;
				font-weight: 400;
				background: #091326;
				color: #fff;
				width: 25%;
				border: 1px solid #091326;
				transition: all 0.3s ease;

				&:disabled {
					color: #091326;
					background: $gold;
				}
			}
		}
	}

	&_content {
		width: 80%;
		height: 35vh;
		background-color: rgba($color: #000000, $alpha: 0.25);
		margin: 0 auto;
		overflow-y: scroll;
		padding: 0 4%;
		padding-top: 2%;
		margin-bottom: 5%;

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.1);
		}

		h2 {
			text-align: left;
			font-size: 1.7vw;
			margin: 0;
			margin-bottom: 4%;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
	}
}

@media (max-width: 1024px) {
	.fbi-garage {
		width: 80%;
	}
}
