.start-screen {
	@extend %window;

	position: relative;
	background-image: url(../../../images/start-screen_bg.jpg);
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 75%;
	padding: 1% 5% 2% 30%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&::before {
		@extend %pseudo;

		bottom: 20%;
		left: 5%;
		width: 28%;
		height: 50vh;
		background-color: #e4e4e4;
	}

	&::after {
		@extend %pseudo;

		bottom: 20%;
		left: 5%;
		width: 28%;
		height: 60vh;
		background-image: url(../../../images/girl2.png);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center bottom;
		z-index: 2;
	}

	p {
		font-size: 1.2vw;
		font-weight: 300;
	}

	&_title {
		margin: 0;
		width: 50%;
		font-weight: 700;
		color: $gold;
	}

	&_gps {
		display: flex;
		justify-content: space-between;
		align-items: center;

		p {
			color: $gold;
		}

		button {
			margin-left: 1%;
		}
	}
}

@media (max-width: 1024px) {
	.start-screen {
		&::before {
			height: 40vh;
		}
	}
}
