.security-offer {
	@extend %window;

	width: 60%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../../images/factions/security/security_offer-bg.jpg);
	padding: 2% 4%;
	color: #fff;
	border-color: $pink;

	&_title {
		border-color: $pink;
		width: 60%;
		margin-bottom: 4%;
	}

	h3 {
		font-size: 2vw;
		text-align: center;
	}

	p {
		padding: 0 7%;
		font-size: 1.2vw;
		text-align: center;
	}

	&_footer {
		padding-top: 5%;
		display: flex;
		align-items: center;
		justify-content: space-between;

		.buttons {
			width: 38%;
			display: flex;
			justify-content: space-between;
		}
	}

	&_price {
		width: 50%;
		font-size: 1.3vw;

		span {
			margin-left: 4%;
			font-size: 2vw;
			font-weight: 700;
			border-bottom: 1px solid rgb(155, 15, 61);
		}
	}
}
