.hud_location {
	position: absolute;
	bottom: 2%;
	padding-left: 0.5%;
	border-left: 1px solid $gold;

	&--green &-street {
		color: #3ce845;
	}

	&-street {
		margin: 0;
		font-size: 1.1vw;
		color: $gold;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
	}

	&-zone {
		margin: 0;
		font-size: 0.9vw;
		font-weight: 300;
		line-height: 1;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
	}
}
