.house {
	@extend %window;

	position: relative;
	width: 60%;
	padding: 1% 3%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/house/house-bg.jpg);

	&_title {
		width: 80%;
	}

	&--owned {
		height: 40vw;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		.house_footer {
			width: 35%;
			display: flex;
			justify-content: space-between;
			position: absolute;
			bottom: 5%;
			right: 3%;

			button {
				width: 50%;
				margin-right: 1%;
			}
		}
	}

	&_tab {
		width: 85%;
	}

	&_controls {
		padding: 5%;
		width: 80%;
		display: flex;
		justify-content: space-between;

		&-item {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 8vw;
			height: 8vw;
			border: 1px solid #fff;
			transition: opacity 0.3s ease;

			&.active {
				background: $gold;
				border: 1px solid $gold;

				.icon {
					background: $purple;
				}

				h3 {
					color: $purple;
					font-weight: 400;
				}

				&:hover {
					opacity: 1;
				}
			}

			&:hover {
				opacity: 0.5;
			}
		}

		.icon {
			display: block;
			width: 4vw;
			height: 4vw;
			margin-bottom: 4%;
			-webkit-mask-size: cover;
			-webkit-mask-repeat: no-repeat;
			background: $gold;

			&-entrance {
				-webkit-mask-image: url(../../../images/house/exit.svg);
			}

			&-tax {
				-webkit-mask-image: url(../../../images/house/home.svg);
			}

			&-sellGov {
				-webkit-mask-image: url(../../../images/house/hand.svg);
			}

			&-sell {
				-webkit-mask-image: url(../../../images/partnership.svg);
			}
		}

		h3 {
			margin: 0;
			font-size: 1vw;
			font-weight: 300;
		}
	}

	&_tax {
		display: flex;
		flex-direction: column;
		align-items: center;

		input {
			margin-bottom: 2%;
			width: 100%;
			background: none;
			border: none;
			border-bottom: 2px solid $gold;
			color: #fff;
			font-family: inherit;
			font-weight: 300;
			font-size: 2.2vw;
			text-align: center;
			outline: none;

			&::placeholder {
				color: #fff;
			}
		}

		&-price {
			width: 50%;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			margin-left: auto;
			text-align: right;

			h4 {
				font-weight: 300;
				font-size: 1.6vw;
				margin: 0;
			}

			.price {
				padding-left: 2%;
				font-size: 1.4vw;
				font-weight: 700;
				color: $gold;
			}
		}
	}

	&_gov-sell {
		padding-bottom: 4%;
		display: flex;
		justify-content: center;
		align-items: center;

		h2 {
			margin: 0;
			margin-right: 15px;
			font-weight: 300;
			font-size: 1.8vw;
		}

		span {
			width: 50%;
			font-size: 2.4vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
			border-bottom: 1px solid $gold;
		}

		input {
			border: none;
			border-bottom: 1px solid $gold;
			background: transparent;
			outline: none;
			width: 40%;
			font-size: 2vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
		}
	}

	&_sell {
		padding: 0 8%;

		label {
			display: flex;
			justify-content: space-between;
		}

		h2 {
			margin: 0;
			margin-right: 15px;
			font-weight: 300;
			font-size: 1.8vw;
		}

		&-price {
			padding-top: 5%;
			display: flex;
			justify-content: flex-end;
			align-self: center;
			font-weight: 400;
			text-align: right;
			font-size: 1.3vw;

			h4 {
				margin: 0;
				font-weight: 400;
				margin-right: 1%;
			}

			span {
				color: $gold;
			}
		}

		input {
			border: none;
			border-bottom: 1px solid $gold;
			background: transparent;
			outline: none;
			width: 60%;
			font-size: 2vw;
			font-weight: 700;
			color: $gold;
			text-align: center;
		}
	}

	&_info {
		display: flex;
		width: 100%;
		align-items: center;

		&-descr {
			width: 50%;
		}
	}

	&--unowned {
		position: relative;
		padding-right: 15%;

		.house_price {
			margin-left: auto;
			width: 70%;
			border-top: 1px solid $gold;

			.price {
				font-weight: 700;
				font-size: 2.5vw;
				color: $gold;
			}
		}

		.house_footer {
			padding: 5% 2%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
		}
	}

	&_info {
		padding-top: 4%;
		font-size: 1.2vw;
		font-weight: 300;
		margin-bottom: 4%;

		p {
			margin: 0;
			margin-bottom: 1%;
			text-align: left;
		}

		span {
			margin-left: 1%;
			color: $gold;
		}
	}

	&_padlock {
		margin-right: 3%;
		display: flex;

		&-item {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 5vw;
			height: 5vw;
			border: 1px solid #fff;
			padding: 5px;

			&.active {
				border: 1px solid $gold;
				background: $gold;

				svg {
					fill: $purple;
				}
			}
		}

		svg {
			width: 4vw;
			height: 4vw;
			fill: #fff;
		}
	}
}
