.ems-docs {
	border: 1px solid #000;
	background: url(../../../../images/ems-docs.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	min-height: 95vh !important;
	position: absolute;
	width: 125vh;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	.btn-exit {
		position: absolute;
		top: 0;
		right: 5px;
		font-size: 2vw;
		color: #b71c00;
	}

	&_info {
		position: absolute;
		top: 55%;
		left: 25%;
		transform: translate(-50%, -50%);
		text-align: center;

		p {
			margin: 0;
			font-weight: 700;
			color: #000;

			&.name {
				font-size: 1.5vw;
			}

			&.rank {
				font-size: 2.2vw;
				text-transform: uppercase;
				margin-bottom: 13vh;
			}
		}
	}
}

@media (max-width: 1024px) {
	.ems-docs {
		&_info {
			p {
				&.name {
					font-size: 2vw;
				}

				&.rank {
					font-size: 2.8vw;
				}
			}
		}
	}
}
