div.rc-slider {
	padding: 0;
	height: 1vh;
	border-radius: 0px;

	&-rail {
		height: inherit;
		background: #fff;
		border-radius: 0px;
	}

	&-track {
		height: inherit;
		background: $gold;
		border-radius: 0px;
	}

	&-step {
		height: inherit;
	}

	&-handle {
		margin-top: -0.3vw;
		width: 1vw;
		height: 1vw;
		border-radius: 0px;
		background: $gold;
		border: 1px solid $gold;

		&:focus {
			background-color: $gold;
			box-shadow: 0 0 5px $gold;
			border: 1px solid $gold;
		}

		&:hover {
			border: 1px solid $gold;
		}

		&:active {
			border-color: $gold;
			box-shadow: 0 0 5px $gold;
		}
	}
}
