.bank {
	@extend %window;

	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	padding-bottom: 3%;
	width: 70%;
	border: none;
	background-image: url(../../../images/bank-bg.jpg);

	.btn-close {
		position: absolute;
		top: 4%;
		right: 1%;
		font-size: 1.5vw;
		font-weight: 500;
		color: #fff;
	}

	&_header {
		position: relative;
		padding: 2%;
		padding-bottom: 4%;
		display: flex;
		justify-content: space-between;

		.logo {
			img {
				display: block;
				width: auto;
				height: 3vw;
			}

			p {
				margin: 0;
				text-align: center;
				font-size: 1vw;
				font-weight: 500;
				opacity: 0.95;
			}
		}
	}

	&-card {
		position: absolute;
		left: 3%;
		bottom: -15%;
		padding: 1% 2%;
		height: 12vw;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 33%;
		background: url(../../../images/bank-card.jpg) no-repeat;
		background-size: cover;
		border-radius: 1vw;
		box-shadow: 5px 25px 25px -20px rgba($color: $purple, $alpha: 1);

		.name {
			margin: 0;
			font-size: 1.1vw;
			font-weight: 300;
		}

		.number {
			margin: 0;
			font-size: 1.2vw;
			font-weight: 500;
		}
	}

	&_balance {
		padding-top: 10%;
		width: 60%;

		&-item {
			margin-top: 0;
			margin-bottom: 2%;
			font-weight: 700;

			.price {
				color: $gold;
			}
		}
	}

	&_operations {
		position: absolute;
		right: 10%;
		margin-top: -2%;
		width: 45%;
		display: flex;
		justify-content: space-between;

		&-item {
			@extend %button;

			padding: 4%;
			width: 49%;
			background-color: $purple;
			border-radius: 1vw;
			font-size: 1.2vw;
			font-weight: 700;
			color: $gold;
			box-shadow: 5px 20px 25px -15px rgba($color: $purple, $alpha: 1);
			transition: all 0.3s ease;

			&.is-active {
				background-color: #fff;
				color: $purple;
			}
		}
	}

	&_form {
		padding: 8% 5%;
		background-color: #eee;

		form {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		&-field {
			padding: 4% 6%;
			width: 60%;
			border-radius: 1vw;
			background-color: #fff;
			box-shadow: 5px 25px 25px -20px rgba($color: $purple, $alpha: 0.7);

			input {
				width: 100%;
				border: none;
				border-bottom: 1px solid $purple;
				text-align: center;
				outline: none;
				font-size: 1.7vw;
				line-height: 1.5;
				font-weight: 700;
				color: $purple;

				&::placeholder {
					color: $purple;
				}
			}
		}

		&-submit {
			@extend %button;

			position: absolute;
			bottom: -30%;
			padding: 2%;
			width: 30%;
			background-color: $purple;
			border-radius: 0.8vw;
			font-size: 1.5vw;
			font-weight: 700;
			color: $gold;
			transition: box-shadow 0.3s ease;

			&:hover {
				box-shadow: 5px 20px 25px -15px rgba($color: $purple, $alpha: 1);
			}
		}
	}
}
