.cityhall-licenses {
	@extend %window;

	width: 70%;
	padding: 1% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/cityhall/cityhall-bg.jpg);

	&_list {
		margin-top: 2%;
		padding: 1% 5%;
		height: 50vh;
		overflow-y: scroll;

		li {
			display: flex;
			justify-content: space-between;
			align-items: flex-end;
			margin-bottom: 6%;

			h3 {
				margin: 0;
				padding-left: 2%;
				margin-left: 2%;
				border-left: 1px solid $gold;
				font-size: 1.8vw;
				line-height: 1.5;
				font-weight: 400;
			}

			.desc {
				margin: 0;
				position: absolute;
				right: 0;
				bottom: -3vh;
				font-size: 1vw;
				font-weight: 300;
				color: $gold;
			}
		}
	}

	&_item {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		margin-bottom: 6%;

		&.active {
			svg {
				fill: $gold;
			}

			.cart {
				border: 1px solid $gold;
				background: gold;

				svg {
					fill: $purple;
				}

				&:hover {
					opacity: 1;
				}
			}
		}

		&.disabled {
			pointer-events: none;

			svg {
				opacity: 0.6;
			}

			.cart {
				opacity: 1;
			}
		}

		h3 {
			width: 100%;
			margin: 0;
			padding-left: 15px;
			border-left: 1px solid $gold;
			font-size: 2vw;
			line-height: 1.5;
			font-weight: 400;
		}

		.desc {
			margin: 0;
			position: absolute;
			right: 0;
			bottom: -3vh;
			font-size: 1vw;
			color: $gold;
		}

		.icon {
			width: 4vw;
			height: 4vw;
			fill: #fff;
		}

		.cart {
			width: 4vw;
			height: 4vw;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 1px solid #fff;
			transition: all 0.3s ease;

			svg {
				width: 2.5vw;
				height: 2.5vw;
			}

			&:hover {
				opacity: 0.5;
			}
		}

		.price {
			width: 22%;
			margin: 0;
			text-align: center;
			color: $gold;
			font-size: 2vw;
			line-height: 1.5;
			font-weight: 700;
			padding-bottom: 5px;
			border-bottom: 1px solid $gold;
		}
	}

	&_info {
		display: flex;
		align-items: center;
		width: 60%;
		position: relative;
		padding-bottom: 5px;
		border-bottom: 1px solid #fff;
	}

	&_price {
		width: 50%;
		display: flex;
		align-items: center;

		h4 {
			margin: 0;
			font-size: 2vw;
			font-weight: 300;
		}

		.price {
			margin-left: 2%;
			font-size: 2vw;
			font-weight: 700;
			color: $gold;
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 50px;

		.buttons {
			display: flex;
			justify-content: space-between;
			width: 31%;
		}
	}
}
