@import './reports';

.admin {
	height: 100%;
	opacity: 0.8;
	overflow: hidden;

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-weight: 700;
		font-size: 1.7vw;
		color: #fff;
	}

	&-tabs {
		min-height: 100%;
		display: flex;
		justify-content: space-between;

		&_list {
			@extend %list;

			position: relative;
			padding-top: 2%;
			margin-right: 5%;
			width: 22%;
			background: $purple;

			li {
				padding: 2% 8%;
				font-size: 1.3vw;
				font-weight: 300;
				outline: none;

				&.is-active {
					color: $purple;
					background: $gold;
				}
			}
		}

		&_panel {
			padding: 3% 4%;
			width: 70%;
			display: none;
			background: $purple;

			&.is-active {
				display: block;
			}

			& > div {
				display: flex;
				flex-direction: column;
			}
		}
	}

	&_tab-list {
		margin-bottom: 5%;
		display: flex;
	}

	&_player-btn {
		position: absolute;
		bottom: 1%;
		left: 2%;
		width: 96%;
		font-size: 1.5vw;
	}

	&_player-list {
		background: rgba(47, 15, 66, 0.95);
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
	}

	&_tab-item {
		@extend %button;

		width: 50%;
		padding: 1% 2%;
		font-size: 1.5vw;
		font-weight: 300;
		color: #fff;
		border: 1px solid $gold;

		&.is-active {
			color: $purple;
			background: $gold;
		}
	}

	label {
		position: relative;
		width: 100%;
	}

	&_error {
		position: absolute;
		margin: 0;
		padding: 5px 0;
		font-size: 1.2vw;
		font-weight: 300;
		color: red;
	}

	input {
		margin-bottom: 2%;
		padding: 1% 2%;
		display: block;
		background: transparent;
		border: 1px solid #fff;
		font-size: 1.6vw;
		color: #fff;
		outline: none;
		transition: opacity 0.3s ease;
	}

	button[type='submit'] {
		margin: 0 auto;
		width: 50%;
	}

	form {
		display: flex;
		flex-direction: column;
	}

	.checkbox {
		display: flex;
		font-size: 1.2vw;
		line-height: 1;
		color: #fff;
		font-weight: 300;
	}

	&_ban {
		form {
			padding: 0 5%;
			display: flex;
			flex-direction: column;
		}

		input {
			margin-bottom: 2%;
			padding: 1% 2%;
			background: transparent;
			border: 1px solid #fff;
			font-size: 1.6vw;
			color: #fff;
			outline: none;
			transition: opacity 0.3s ease;

			&:hover {
				opacity: 0.7;
			}
		}

		&-reason {
			width: 100%;
		}
	}

	&_teleport {
		form {
			display: flex;
			flex-direction: column;
		}

		.fields {
			display: flex;
			justify-content: space-between;

			input {
				width: 30%;
				text-align: center;
			}
		}
	}

	&_vehicle {
		form {
			display: flex;
			flex-direction: column;
		}

		&-selector {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}

		.options {
			width: 50%;
		}
	}

	.options {
		margin-bottom: 2%;
		font-size: 1.2vw;
		color: #fff;

		&__control {
			border: 1px solid #fff;
			border-radius: 0px;
			background: transparent;
			color: #eee;
			outline: none;

			&--is-focused {
				border: 1px solid #fff;
			}

			&:hover {
				border: 1px solid #fff;
			}
		}

		&__single-value,
		&__placeholder,
		&__indicator {
			color: currentColor;
		}

		&__value-container {
			padding: 1% 2%;
		}

		&__menu {
			background: $purple;
			border-radius: 0.5vw;

			::-webkit-scrollbar {
				width: 0;
				background: transparent;
			}

			&-list {
				max-height: 50vh;
			}
		}

		&__option {
			border: 1px solid #fff;
			padding: 1% 2%;
			color: #fff;

			&--is-focused {
				background: transparent;
			}
		}
	}

	&_faction {
		display: flex;
		flex-direction: column;
	}

	&_datepicker {
		border-radius: 0;
		border: none;
	}
}
