.resources-selling {
	@extend %window;

	position: relative;
	background-image: url(../../../images/legal-bg.jpg);
	width: 50%;
	padding: 1% 3%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	&_current {
		@extend %button;

		margin-left: auto;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		white-space: nowrap;
		font-size: 1.2vw;
		font-weight: 300;
		color: #fff;

		svg {
			margin-left: 1%;
			width: 2vw;
			height: 2vw;
			fill: $gold;
		}
	}

	&_items {
		padding-top: 3%;
		display: flex;

		ul {
			position: absolute;
			top: 35%;
			right: 5%;
			padding: 2%;
			width: 40%;
			background-image: url(../../../images/legal-bg.jpg);
			text-align: center;
			box-shadow: 0px 4px 4px 4px rgba(0, 0, 0, 0.1);
			z-index: 3;
		}

		li {
			margin-top: 2%;
			padding: 1% 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-weight: 300;

			&:hover {
				opacity: 0.5;
			}

			img {
				width: 2vw;
				height: 2vw;
				filter: invert(1);
			}

			&:not(:last-of-type) {
				border-bottom: 1px solid $gold;
			}
		}
	}

	&_count {
		margin-bottom: 10%;
		padding-top: 3%;
		display: flex;
		align-items: center;

		h3 {
			margin: 0;
			margin-right: 3%;
			width: 22%;
			font-size: 1.3vw;
			font-weight: 300;

			b {
				color: $gold;
			}
		}
	}

	&_all-btn {
		margin-left: auto;
	}

	&_price {
		width: 40%;
	}

	&_footer {
		display: flex;
		align-items: center;
		justify-content: space-between;

		.buttons {
			width: 48%;
			display: flex;
			justify-content: space-between;

			button {
				width: 49%;
			}
		}
	}

	&--illegal {
		background-image: url(../../../images/illegal-bg.jpg);
		border-color: $pink;

		.resources-selling {
			&_title {
				border-color: $pink;
			}

			&_current {
				svg {
					fill: $pink;
				}
			}

			&_count {
				b {
					color: $pink;
				}

				svg {
					fill: #fff;
				}
			}

			&_items {
				ul {
					background-image: url(../../../images/illegal-bg.jpg);

					li {
						border-color: $pink;
					}
				}
			}

			&_price {
				border-color: $pink;
			}
		}
	}
}
