#report {
	background-image: url(../../../images/phone/report_bg.jpg);
	background-size: cover;
	background-position: center;
	text-align: center;

	.report {
		height: inherit;

		&_title {
			margin-bottom: 25%;

			h2 {
				margin: 0;
				text-transform: uppercase;
				font-size: 2.8vw;
				line-height: 1.3;
			}

			p {
				margin: 0;
				font-size: 1.1vw;
				font-weight: 300;
			}
		}

		&_form {
			display: flex;
			flex-direction: column;
			color: #1fa3ff;

			input {
				margin-bottom: 8%;
				padding: 4% 6%;
				border-radius: 3vw;
				border: none;
				outline: none;
				background: #fff;
				font-size: 1.2vw;
				line-height: 1.5;
				text-align: center;
				color: currentColor;

				&::placeholder {
					color: inherit;
				}
			}

			.btn-submit {
				margin: 0 auto;
				padding: 4% 8%;
				width: 70%;
				border-radius: 2vw;
				border: 1px solid #fff;
				font-size: 1.3vw;
				color: #fff;
				transition: all 0.3s ease;

				&:hover {
					color: #1fa3ff;
					background: #fff;
				}
			}
		}
	}
}
