.fishing-shop {
	@extend %window;

	top: 50%;
	left: 50%;
	padding: 2%;
	width: 50%;
	transform: translate(-50%, -50%);
	background-image: url(../../../images/fishing-bg.jpg);
	color: #fff;

	&_footer {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;

		button {
			padding: 1% 4%;
		}

		.exit-btn {
			margin-right: auto;
		}
	}

	&_price {
		width: 40%;
		margin-right: 3%;
		border-bottom: 1px solid $gold;
		font-size: 2vw;
		text-align: right;
	}

	&_items {
		margin: 0 auto;
		padding: 4% 0;
		width: 80%;

		&_list {
			height: 20vh;
			overflow-y: scroll;
			padding-right: 20px;
		}

		&-item {
			padding: 1%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #fff;

			&.is-active {
				border-color: $gold;
			}

			img {
				width: 3vw;
				height: 3vw;
				filter: invert(1);
			}

			.capacity {
				font-size: 1.2vw;
				line-height: 1.2;
				color: $gold;
			}
		}
	}
}
