.login,
.code,
.reset {
	.auth {
		&_remember {
			margin-top: 1%;
			font-family: 'FuturicaBook';
			font-size: 1.3vw;
			text-decoration: none;
			color: #fff;
			margin-bottom: 20%;
		}

		&_form-code {
			div {
				width: 70%;
				text-align: right;

				label {
					width: 100%;
					margin-bottom: 10%;
				}
			}
		}
	}
}

.login .auth {
	&_form {
		label:last-of-type {
			margin-bottom: 0;
		}
	}
}

.recovery-code .auth {
	&_form {
		min-height: 15vw;
	}
}
