.underlined-title {
	margin: 0 auto;
	padding-bottom: 1%;
	border-bottom: 1px solid $gold;
	text-align: center;
	font-weight: 300;
	font-size: 2vw;
	white-space: nowrap;
}

.price {
	&--underlined {
		padding-left: 20%;
		display: block;
		width: 100%;
		border-bottom: 1px solid $gold;
		text-align: right;
		font-weight: 300;
		font-size: 2vw;
	}

	&--drivepoints {
		display: flex;
		align-items: center;
		justify-content: flex-end;

		.drivepoint-icon {
			padding-left: 2%;
			width: 2.2vw;
			height: 2.2vw;
			fill: #fff;
		}
	}
}

.count-controls {
	display: flex;
	flex-direction: column;

	button {
		@extend %button;

		width: 1vw;
		height: 1vw;
		color: $gold;

		svg {
			width: inherit;
			height: inherit;
			color: currentColor;
		}

		&.inc-btn {
			margin-bottom: 0.5vw;
		}

		&:hover {
			opacity: 0.7;
		}
	}
}

input.apple-switch {
	position: relative;
	-webkit-appearance: none;
	outline: none;
	width: 3.5vw;
	height: 2vw;
	background-color: #fff;
	border: 1px solid #d9dadc;
	border-radius: 2vw;
	box-shadow: inset -42% 0 0 0 #fff;
}

input.apple-switch:after {
	content: '';
	position: absolute;
	top: 1%;
	left: 1%;
	background: transparent;
	width: 1.8vw;
	height: 1.8vw;
	border-radius: 50%;
	box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.2);
}

input.apple-switch:checked {
	box-shadow: inset 1.4vw 0 0 0 #4ed164;
	border-color: #4ed164;
}

input.apple-switch:checked:after {
	left: 42%;
	box-shadow: -2px 4px 3px rgba(0, 0, 0, 0.05);
}
