#courses {
	background: #000;
	text-align: left;

	.courses {
		&_items {
			padding: 0 2%;
			height: 28vw;
			overflow-y: scroll;

			&:hover {
				&::-webkit-scrollbar-thumb {
					border: none;
					background-clip: padding-box;
					background-color: #b8b8b8;
					border-radius: 6px;
				}
			}

			&::-webkit-scrollbar {
				width: 2px;
				opacity: 0;
			}

			&::-webkit-scrollbar-track {
				background-color: transparent;
			}

			&::-webkit-scrollbar-thumb {
				background-color: transparent;
			}

			li {
				padding: 5% 3%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 1.1vw;
				font-weight: 300;

				&:not(:last-of-type) {
					border-bottom: 1px solid #272729;
				}

				.name {
					margin: 0;
					max-width: 70%;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					font-weight: inherit;
				}
			}
		}
	}
}
