.circle-menu {
	top: 50%;
	left: 50%;
	width: 30vw;
	height: 30vw;
	min-height: auto !important;
	position: absolute;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);

	&_center {
		top: 50%;
		left: 50%;
		z-index: 1;
		width: 12vw;
		height: 12vw;
		background: linear-gradient(180deg, rgb(71, 23, 100) 50%, rgb(78, 19, 116) 100%);
		position: absolute;
		border-radius: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		border: 4px solid $gold;
		color: $gold;
		font-weight: 700;
		font-size: 1.3vw;
		outline: none;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}

	&_items {
		width: 100%;
		height: 100%;
		overflow: hidden;
		position: relative;
		border-radius: 50%;
	}

	&_item {
		top: 0;
		left: 0;
		width: 50%;
		height: 50%;
		cursor: pointer;
		position: absolute;
		-webkit-transform-origin: 100% 100%;
		transform-origin: 100% 100%;
		background: rgba($color: $purple, $alpha: 0.6);
		font-size: 4vw;
		font-weight: 300;
		color: $gold;

		&:hover {
			background: rgba($color: $gold, $alpha: 0.7);

			.icon {
				color: $purple;
				fill: $purple;
			}
		}

		&:first-child {
			transform: rotate(-45deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(45deg);
			}
		}

		&:nth-child(1) {
			transform: rotate(0deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(0deg);
			}
		}

		&:nth-child(2) {
			transform: rotate(45deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-45deg);
			}
		}

		&:nth-child(3) {
			transform: rotate(90deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-90deg);
			}
		}

		&:nth-child(4) {
			transform: rotate(135deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-135deg);
			}
		}

		&:nth-child(5) {
			transform: rotate(180deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-180deg);
			}
		}

		&:nth-child(6) {
			transform: rotate(225deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-225deg);
			}
		}

		&:nth-child(7) {
			transform: rotate(270deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-270deg);
			}
		}

		&:nth-child(8) {
			transform: rotate(315deg) skew(45deg);

			.icon {
				transform: skew(-45deg) rotate(-315deg);
			}
		}
	}

	.icon {
		width: 30%;
		height: 30%;
		left: 50%;
		top: 55%;
		transform: translate(-50%, -50%);
		position: absolute;
		fill: $gold;
	}
}
