.sheriff-leaderboard {
	width: 90vw;
	min-height: auto !important;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background: url(../../../../images/sheriff_bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1% 4%;
	color: #fff;

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-size: 1.5vw;
		color: #fff;
	}

	&_logo {
		margin-bottom: 3%;
		display: flex;
		flex-direction: column;
		font-style: italic;
		font-weight: 800;
		text-align: left;

		h1 {
			margin: 0;
			font-size: 2.5vw;
		}

		h2 {
			position: relative;
			margin: 0;
			padding-left: 20%;
			font-size: 3vw;
			color: $sandy;

			&::before {
				@extend %pseudo;

				clip-path: polygon(7% 0, 100% 0, 94% 100%, 0 100%);
				width: 20%;
				height: 100%;
				left: 0;
				background: $sandy;
			}
		}

		h3 {
			position: absolute;
			top: 9%;
			left: 4%;
			width: 20%;
			text-align: center;
			font-size: 1.2vw;
			color: #1f1c16;
		}
	}

	&_content {
		padding: 1% 2%;
		background-color: rgba($color: #252014, $alpha: 0.7);
	}

	.header {
		margin-bottom: 1%;
		display: flex;
		justify-content: space-between;

		h3 {
			margin: 0;
			width: 33%;
			text-align: center;
			font-size: 1.5vw;
			line-height: 2;

			&:first-of-type {
				text-align: left;
			}
		}
	}

	.list {
		height: 60vh;
		overflow-y: scroll;
		padding-right: 1%;

		&::-webkit-scrollbar-track {
			background-color: #fff;
		}

		&::-webkit-scrollbar-thumb {
			background: $sandy;
		}

		li {
			padding: 1% 0;
			display: flex;
			border-bottom: 2px solid #fff;

			p {
				margin: 0;
				width: 33%;
				font-size: 1.5vw;
				font-weight: 700;
				color: $sandy;
				text-align: center;

				&:first-of-type {
					padding-left: 5%;
					text-align: left;
				}
			}
		}
	}
}
