.police-weapons {
	display: flex;
	padding: 0 3%;
	justify-content: space-between;
	margin: 0 auto;
	background: url(../../../../images/police_bg.jpg) no-repeat;
	background-size: cover;

	&_sidebar {
		padding-top: 2%;
		width: 20%;
		display: flex;
		flex-direction: column;

		.logo {
			display: flex;
			align-items: center;
			margin-bottom: 4%;

			h1 {
				font-size: 1.5vw;
				margin-left: 4%;
			}

			img {
				width: 30%;
			}
		}

		.categories_item {
			background: #f9c422;
			margin-bottom: 4%;
			text-align: center;
			padding: 2% 4%;
			transition: all 0.5s ease;
			color: #1a3f84;
			font-weight: 700;

			&:hover {
				opacity: 0.5;
			}

			&.active {
				opacity: 0.5;
			}
		}
	}

	.ammo {
		margin-top: auto;
		padding: 0 1%;

		&_list {
			padding-right: 4%;
			border-left: 1px solid #4cbae8;
		}

		&_item {
			display: flex;
			justify-content: space-between;
			padding-left: 4%;
			margin-bottom: 4%;

			input {
				width: 80%;
				border: 1px solid #fff;
				background: transparent;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 1.2vw;
				color: #fff;
			}

			span {
				width: 2vw;
				height: 2vw;
				margin-right: 1%;
				padding: 2px;
				border: 1px solid #fff;
			}

			svg {
				display: block;
				width: 1.5vw;
				height: 1.5vw;
				margin: 0 auto;
				fill: rgb(155, 155, 155);
			}

			&.active {
				span {
					background: #fff;
				}

				i {
					background-color: #1a3f84;
				}
			}
		}
	}

	&_content {
		display: flex;
		width: 75%;
		padding-top: 10%;
		flex-direction: column;
	}

	&_list {
		background-color: rgba($color: #000, $alpha: 0.5);
		height: 65vh;
		overflow-y: scroll;
		margin-bottom: 2%;

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.1);
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding: 0 4%;
			padding-top: 2%;
		}

		li {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;
			width: 30%;
			margin-right: 2%;
			margin-bottom: 2%;
			border: 1px solid #f9c422;
			padding: 1%;
			padding-top: 2%;

			img {
				height: 4vw;
				width: 100%;
				margin-bottom: 8%;
				filter: invert(1) brightness(0.8);
			}

			.price {
				font-size: 1.5vw;
			}

			.btn-buy {
				width: 40%;
				padding: 2% 3%;
				background: #1a3f84;
				color: #f9c422;
				font-family: inherit;
				font-size: 1vw;
				transition: opacity 0.3s ease;

				&:hover {
					opacity: 0.5;
				}

				&.active {
					background: #f9c422;
					color: #1a3f84;
					font-weight: 300;

					&:hover {
						opacity: 1;
					}
				}
			}
		}
	}

	&_footer {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 4%;
		padding-bottom: 1%;
		margin-top: auto;
		width: 100%;

		.buttons {
			width: 35%;
			display: flex;

			button {
				margin-left: 1%;
				width: 50%;
				border: 1px solid #4cbae8;
			}
		}
	}
}
