.player-passport {
	background: url(../../../images/passport.jpg) no-repeat;
	background-size: cover;
	background-position: top center;
	min-height: auto !important;
	display: flex;
	flex-direction: column;
	position: absolute;
	width: 55%;
	padding: 12% 20% 5% 2%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	.btn-exit {
		position: absolute;
		top: 5px;
		right: 5px;
		font-weight: 700;
		font-size: 1.7vw;
		color: #fff;
	}

	&_info {
		text-align: left;
		color: #202020;

		p {
			padding: 0 2%;
			background: linear-gradient(
				180deg,
				rgba(255, 255, 255, 0.3) 10%,
				rgba(255, 255, 255, 0.5) 50%,
				rgba(255, 255, 255, 0.3) 90%
			);

			border-bottom: 1px solid #fff;
			font-size: 1.2vw;
			margin-top: 0;
			font-weight: 300;
		}

		.title {
			display: block;
			font-weight: 700;
			font-size: 1.3vw;
		}
	}
}
