.fbi-pc {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	height: 100%;
	margin: 0 auto;
	padding: 2%;
	background: url(../../../../images/fbi_bg.jpg) no-repeat;
	background-size: cover;

	&_logo {
		align-self: flex-start;
		width: 100%;

		h1 {
			text-align: left;
			font-size: 3vw;
			font-weight: 700;
			margin: 0;
			padding-left: 10%;

			b {
				color: $gold;
			}
		}
	}

	&_violators {
		width: 100%;
		display: flex;
		flex-direction: column;
		padding: 0 4%;
		padding-top: 10px;

		.search-fields {
			margin-left: auto;
			display: flex;
		}

		input {
			margin-left: 1%;
			width: 50%;
			font-size: 1vw;
			font-weight: 700;
			font-family: inherit;
			border: none;
			outline: none;
			color: #091326;
			padding: 2% 4%;
			text-align: right;

			&::placeholder {
				font-weight: 300;
				color: #091326;
			}
		}

		&-list {
			@extend %list;

			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding-top: 2%;
			margin-bottom: 20px;
		}

		&-item {
			display: flex;
			background: #fff;
			width: 100%;
			border-left: 6px solid $gold;
			padding: 10px;
			margin-bottom: 10px;

			&:nth-child(even) {
				background: #d3e2ff;
				border-left: 6px solid #153d89;
			}

			div {
				width: 45%;

				&:not(:last-of-type) {
					margin-right: 20px;
				}
			}

			h3 {
				margin: 0;
				margin-bottom: 5px;
				font-size: 1.2vw;
				font-weight: 300;
				text-align: left;
				color: #091326;
			}

			p {
				margin: 0;
				padding: 2px 5px;
				background: #091326;
				font-size: 1.3vw;
				font-weight: 700;
				color: $gold;
			}
		}
	}

	&_police-members {
		padding: 0 4%;

		.header {
			display: flex;
			justify-content: space-between;
			margin-bottom: 1%;

			p {
				width: 33%;
				font-size: 1.5vw;
				line-height: 2;
				text-align: center;
				color: $gold;
				margin: 0;
			}
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			margin-bottom: 20px;
		}

		li {
			display: flex;
			background: #fff;
			width: 100%;
			border-left: 6px solid #153d89;
			padding: 10px;
			margin-bottom: 10px;

			&:nth-child(even) {
				background: #d3e2ff;
				border-left: 6px solid $gold;
			}

			div {
				width: 45%;

				&:not(:last-of-type) {
					margin-right: 20px;
				}
			}

			p {
				margin: 0;
				padding: 2px 5px;
				background: #091326;
				font-size: 1.3vw;
				font-weight: 700;
				text-align: center;
				color: #fff;
			}
		}
	}

	&_content {
		width: 78%;
		height: 100%;
		border-top: 2px solid #fff;
		background-color: rgba($color: #000000, $alpha: 0.25);
		height: 72vh;
		overflow-y: scroll;

		&::-webkit-scrollbar-track {
			background-color: rgba(0, 0, 0, 0.1);
		}
	}

	&_sidebar {
		width: 20%;
		display: flex;
		flex-direction: column;

		li {
			margin-bottom: 10px;
			text-align: left;
			padding: 5px 10px;
			transition: all 0.5s ease;
			font-size: 1.5vw;
			color: #fff;
			border: 1px solid #fff;

			&:hover {
				opacity: 0.5;
			}

			&.is-active {
				background: #fff;
				color: #091326;

				&:hover {
					opacity: 1;
				}
			}
		}

		button {
			margin-top: auto;
		}
	}

	&_phone-info {
		display: flex;
		flex-direction: column;
		min-height: 100%;

		form {
			display: flex;
			flex-direction: column;
			width: 50%;
			margin: 0 auto;
			margin-bottom: 4%;

			p {
				font-weight: 700;
				font-size: 1.3vw;
				text-align: left;
				margin-bottom: 5px;
			}

			input {
				width: 100%;
				margin-bottom: 10px;
				border: none;
				border-left: 4px solid $gold;
				padding: 5px 15px;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 2vw;
				font-weight: 700;
				color: $gold;
				background: #0b1d3f;
			}

			button {
				margin-left: auto;
			}
		}

		h2 {
			width: 60%;
			margin: 0 auto;
			margin-top: auto;
			font-size: 1.7vw;
			color: $gold;
			padding-bottom: 5px;
			border-bottom: 1px solid #fff;
			text-align: center;
		}

		.list {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: auto;
			padding: 20px;
			background-color: rgba($color: #000000, $alpha: 0.25);
			min-height: 30vh;
			text-align: left;

			p {
				font-size: 1.3vw;
				font-weight: 300;

				b {
					color: $gold;
				}
			}
		}
	}

	&_licenses {
		padding: 1% 5%;

		form {
			margin-bottom: 2%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			input {
				width: 30%;
				border: none;
				padding: 1% 2%;
				text-align: center;
				outline: none;
				font-family: inherit;
				font-size: 2vw;
				font-weight: 700;
				color: #153d89;

				&::placeholder {
					color: #153d89;
				}
			}
		}

		.list {
			min-height: 30vh;
			padding: 0 4%;
			background-color: rgba($color: #000000, $alpha: 0.25);

			.header {
				margin-bottom: 2%;
				padding: 0 4%;
				display: flex;
				justify-content: space-between;
				border-bottom: 1px solid $gold;

				p {
					margin: 0;
					font-size: 1.2vw;
					font-weight: 700;
					line-height: 2;
					color: $gold;

					&:first-child {
						width: 70%;
						border-right: 1px solid $gold;
					}
				}
			}

			li {
				display: flex;
				justify-content: space-between;
				background: #fff;
				width: 100%;
				border-left: 6px solid $gold;
				padding: 0 1%;
				margin-bottom: 2px;

				&:nth-child(even) {
					background: #faedc8;
				}

				div {
					padding: 1% 0;
					width: 33%;

					&:first-of-type {
						width: 70%;
						border-right: 1px solid #153d89;
					}

					&:last-of-type {
						border-left: 6px solid $gold;
					}
				}

				p {
					margin: 0;
					padding: 2px 5px;
					font-size: 1.3vw;
					font-weight: 700;
					text-align: left;
					color: #153d89;
				}
			}
		}
	}
}
