.vehicle-info {
	position: absolute;
	left: 1%;
	bottom: 2%;
	width: 12%;

	&_item {
		margin-bottom: 2%;
	}

	&_title {
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 1vw;

		h4 {
			margin: 0;
			font-weight: 200;
		}
	}

	&_progress {
		display: block;
		width: 100%;
		height: 1vh;
		color: $gold;
		background-color: #fff;

		&::-webkit-progress-bar {
			background-color: #fff;
		}
		&::-webkit-progress-value {
			background-color: $gold;
		}
	}
}
