.player-info {
	background: url(../../../images/stats-bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	padding: 1% 2%;
	color: #fff;

	&_tab-list {
		ul {
			width: 100%;
			display: flex;
			border-bottom: 1px solid $gold;
		}

		li {
			padding: 0.5% 1%;
			font-size: 1.5vw;
			font-weight: 300;

			&.active {
				background: $gold;
				color: $purple;
			}
		}
	}

	&_tab {
		padding-top: 3%;
	}

	&_exit-btn {
		position: absolute;
		padding: 1% 2%;
		left: 2%;
		bottom: 2%;
	}

	&_stats {
		display: flex;
		flex-direction: column;
		width: 40%;
		height: 70vh;
		margin-left: auto;
		text-align: left;

		&::after {
			@extend %pseudo;

			width: 40%;
			height: 80vh;
			left: 15%;
			bottom: 0;
			background: url(../../../images/girl-gold.png) no-repeat;
			background-size: contain;
			background-position: center bottom;
		}

		p {
			font-size: 1.5vw;
			font-weight: 300;
			margin: 0;
			margin-bottom: 4%;

			span {
				margin-left: 1%;
				font-weight: 700;
				color: $gold;
			}
		}

		&-items {
			padding-top: 4%;
			overflow-y: scroll;
			margin-bottom: 20%;
		}
	}

	&_card-number {
		margin-top: auto;

		p {
			margin-bottom: 1%;
		}

		span {
			font-size: 2.4vw;
			font-weight: 700;
			color: $gold;
		}
	}

	&_docs {
		display: flex;
		justify-content: space-between;

		&-item {
			width: 49%;
		}

		.player-passport {
			width: 100%;
			position: relative;
			background-position: center top;
			height: 60vh;
			padding-top: 18%;
			padding-right: 30%;
		}

		.player-licenses {
			width: 100%;
			position: relative;
			height: 60vh;
			padding-top: 28%;
			padding-right: 36%;
		}

		.btn-exit {
			display: none;
		}
	}
}

@media (max-width: 1024px) {
	.player-info {
		&_docs {
			display: flex;
			justify-content: space-between;

			&-item {
				width: 49%;
			}

			.player-passport {
				background-position: 10%;
				height: 50vh;
				padding-top: 22%;
			}

			.player-licenses {
				background-position: 10%;
				height: 50vh;
				padding-top: 30%;
			}
		}
	}
}
