.hud_help {
	position: absolute;
	top: 35%;
	padding-left: 0.5%;
	width: 15%;

	&-list {
		@extend %list;

		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	&-item {
		display: flex;
		align-items: center;
		padding-right: 4%;
		margin-bottom: 1.2%;
	}

	&-name {
		text-align: center;
		font-size: 0.9vw;
		line-height: 1.8;
		margin: 0;
		margin-right: 0.8em;
		font-weight: 300;
		width: 1.5vw;
		height: 1.5vw;
		background: rgba($color: #000, $alpha: 0.8);
		color: #fff;
		border-radius: 12%;
	}

	&-desc {
		margin: 0;
		text-align: right;
		white-space: nowrap;
		font-size: 0.8vw;
		font-weight: 300;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
	}
}
