.gang-leader_leave {
	display: flex;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.5);

	&-container {
		padding: 2% 5%;
		border-top: 5px solid $gold;
		background: url(../../../images/gang-leader_bg.jpg) no-repeat;
		background-size: cover;
		width: 50%;
		margin: auto;
	}

	p {
		margin-bottom: 4%;
		border-bottom: 1px solid $gold;
		text-align: center;
		font-size: 1.3vw;
		line-height: 2;
	}

	.buttons {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;

		button {
			width: 48%;
		}
	}
}
