@import './contact';
@import './contact-editor';

#contacts {
	background: #fff;
	text-align: left;
	color: #000;

	.phone_content {
		padding: 0;
		padding-top: 5%;
	}

	.contacts {
		.header {
			position: relative;
			margin-bottom: 3%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			h2 {
				margin: 0 auto;
				font-size: 1.2vw;
			}

			.btn-add {
				position: absolute;
				right: 0;

				svg {
					width: 2vw;
					height: 2vw;
					fill: $blue;
				}
			}
		}

		&_search {
			margin: 0 auto 6%;
			width: 90%;

			input {
				background: #e8e8ea;
				color: #757575;
			}
		}

		&_list {
			border-top: 1px solid #eaeaea;

			ul {
				height: 28vw;
				overflow-y: scroll;
			}

			li {
				font-size: 1vw;

				.letter {
					margin: 0;
					padding: 0.5% 0;
					padding-left: 5%;
					background-color: #f2f2f2;
				}

				&:not(:first-of-type) {
					.letter {
						margin-top: -1px;
					}
				}

				.name {
					position: relative;
					margin: 0;
					padding: 2% 0;
					padding-left: 5%;
					font-weight: 700;
					color: #000;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;

					&:hover {
						opacity: 0.5;
					}

					&::after {
						@extend %pseudo;

						bottom: 0;
						left: 5%;
						width: 100%;
						height: 1px;
						background-color: #f2f2f2;
					}
				}
			}
		}
	}
}
